
import React,{Fragment,Component} from 'react';
import './style_authentication.scss'
import PropTypes from 'prop-types'
import { Handles, Rail, Slider, Ticks, Tracks } from 'react-compound-slider';
const railOuterStyle = {
  position: 'absolute',
  width: '100%',
  height: 42,
  transform: 'translate(0%, -50%)',
  borderRadius: 7,
  cursor: 'pointer',
  // border: '1px solid white',
}

const railInnerStyle = {
  position: 'absolute',
  width: '100%',
  height: 14,
  transform: 'translate(0%, -50%)',
  borderRadius: 7,
  pointerEvents: 'none',
  backgroundColor: 'white',
  boxShadow: '0 0 10px  rgba(0,0,0,0.6)'
}

const sliderStyle = {
  position: 'relative',
  width: '100%',
}

class Edit_category extends Component{
    constructor(props){
      super(props)
      this.state ={
        list:[],
        show_sub_slider:false,
        slider_info:[],
        room_array:"",
        temp_rooms_modify:{},
        modal_popup_num:"1",
        temp:[],
        popup_temp_no1:1,
        popup_temp_no2:"1"
      }
      
    }
    calling_props_1(){
      this.props.update_room_special(this.state.popup_temp_no1,this.props.mem)
    }
    calling_props_2(){
      this.props.update_room(this.state.popup_temp_no1,this.props.mem,1)
    }
    vacate_rooms(){
      this.bt()
      this.props.vacate_rooms(this.props.mem)
    }
    go_back(){
        this.props.goback()
    }
    set_room_alloc_sp(m1){
      this.props.update_room("msg",this.props.mem,2,this.state.temp_rooms_modify,m1)
    }
    set_room_alloc(){
      this.props.update_room("msg",this.props.mem,0,this.state.temp_rooms_modify)
    }
    getZero(n){
      // this is a local function to add zeros to the left of room numbers
      let num=n+""
      if(num.length===1){
        return "00"+n
      }else if(num.length===2){
        return "0"+n
      }else{
        return n+""
      }
    }
    add_room(msg){
      if(this.state.show_sub_slider){
        this.setState({show_sub_slider:false});
      }
        setTimeout(()=>{
          this.setState({
            slider_info:[this.state.list[msg]],
            show_sub_slider:true
          })
        },0)
    }
    update_room_for_spec(msg){
      this.setState({
        popup_temp_no1:msg,
        popup_temp_no2:"2",
        modal_popup_num:"4"
      })
      if( document.getElementById("myModal8").style.display = "none"){
        document.getElementById("myModal8").style.display = "block";
      }
    }
    update_room_special(msg){
      this.setState({
        popup_temp_no1:msg,
        popup_temp_no2:"1",
        modal_popup_num:"4"
      })
      if( document.getElementById("myModal8").style.display = "none"){
        document.getElementById("myModal8").style.display = "block";
      }
    }
    change_room_value(msg1,msg2){
      // this function is passed to the silder1 child and 
      // is called from the child to modify the currently allocated rooms of selected category 
      let obj = this.state.temp_rooms_modify
      obj[msg1]=msg2
      this.setState({temp_rooms_modify:obj})
    }
    componentDidMount(){
      let str=""
      this.props.dt["Category_allocation"][this.props.mem]["Room_array"].map(dt=>{
        str=str+dt+", "
      })
      let n=-1,obj={};
      this.props.rooms[this.props.mem].map(dt=>{
        n=n+1;
        obj[n]=dt
      })
  this.setState({
      list:this.props.unallocated_list,
      room_array:str.slice(0,-1),
      temp_rooms_modify:obj
})

    }
    bt(i){
      if( document.getElementById("myModal8").style.display = "block"){
        document.getElementById("myModal8").style.display = "none";
      }
      this.setState({
        modal_popup_num:"1"
      })
    }
    show_popup_vacate_room(){
      if( document.getElementById("myModal8").style.display = "none"){
        document.getElementById("myModal8").style.display = "block";
      }
    }
    show_popup_delete_allocation(msg){
      this.setState({
        modal_popup_num:"2",
        temp:msg
      })
      if( document.getElementById("myModal8").style.display = "none"){
      document.getElementById("myModal8").style.display = "block";
      }
    }
    show_popup_update_cate_alloc(){
      this.setState({
        modal_popup_num:"3"
      })
      if( document.getElementById("myModal8").style.display = "none"){
        document.getElementById("myModal8").style.display = "block";
        }
    }
    show_popup_add_unalocated_rooms(){
      this.setState({
        modal_popup_num:"4"
      })
      if( document.getElementById("myModal8").style.display = "none"){
        document.getElementById("myModal8").style.display = "block";
        }
    }
render(){
  let i=0,tot=1,j=-1;
    return(
        <div >
            <div id="myModal8" className="modal">
            {this.state.modal_popup_num==="1"?<div className="modal-content">
<div className="modal-header">
<h2 style={{color:"white"}}>Message Box</h2>
<span className="close" id='cls' onClick={this.bt.bind(this)}>&times;</span>

</div>

<div className="profile">

    <center>Confirm to Vacate all rooms from this category .<br/> Warning:: Changes made here can't be unchanged.</center>
    
<div className="submit-part">
<button type="submit" className="btn btn-primary" onClick={this.bt.bind(this)} style={{background:"#337ab7"}}>Close</button>
<button type="submit" className="btn btn-primary" onClick={this.vacate_rooms.bind(this)} style={{background:"#337ab7"}}>Confirm</button>
</div>
</div> 
</div>:this.state.modal_popup_num==="2"?<div className="modal-content">
<div className="modal-header">
<h2 style={{color:"white"}}>Message Box</h2>
<span className="close" id='cls' onClick={this.bt.bind(this)}>&times;</span>

</div>

<div className="profile">

    <center>Confirm to Delete extra allocated rooms.</center>
    
<div className="submit-part">
<button type="submit" className="btn btn-primary" onClick={this.bt.bind(this)} style={{background:"#337ab7"}}>Close</button>
<button type="submit" className="btn btn-primary" onClick={this.set_room_alloc_sp.bind(this,this.state.temp)} style={{background:"#337ab7"}}>Confirm</button>
</div>
</div> 
</div>:this.state.modal_popup_num==="3"?<div className="modal-content">
<div className="modal-header">
<h2 style={{color:"white"}}>Message Box</h2>
<span className="close" id='cls' onClick={this.bt.bind(this)}>&times;</span>

</div>

<div className="profile">

    <center>Confirm to update Allocated rooms. <br/>Warning:: Unallocated rooms will be transfered to the unallocated list. It can be allocated by any category.</center>
    
<div className="submit-part">
<button type="submit" className="btn btn-primary" onClick={this.bt.bind(this)} style={{background:"#337ab7"}}>Close</button>
<button type="submit" className="btn btn-primary" onClick={this.set_room_alloc.bind(this)} style={{background:"#337ab7"}}>Confirm</button>
</div>
</div> 
</div>:<div className="modal-content">
<div className="modal-header">
<h2 style={{color:"white"}}>Message Box</h2>
<span className="close" id='cls' onClick={this.bt.bind(this)}>&times;</span>

</div>

<div className="profile">

    <center>Confirm to Save Changes.</center>
    
<div className="submit-part">
<button type="submit" className="btn btn-primary" onClick={this.bt.bind(this)} style={{background:"#337ab7"}}>Close</button>
{this.state.popup_temp_no2==="1"?
<button type="submit" className="btn btn-primary" onClick={this.calling_props_1.bind(this)} style={{background:"#337ab7"}}>Confirm</button>:
<button type="submit" className="btn btn-primary" onClick={this.calling_props_2.bind(this)} style={{background:"#337ab7"}}>Confirm</button>
}
</div>
</div> 
</div>}
            </div>
            <div className="he">
          <div className="head">
            <div> <h5 ><b>Category Editing Window</b></h5></div>
          </div>
          <div style={{paddingTop:"1.5vh", marginRight:"1vw"}}> <a href="#"><input type="submit" className="btn btn-info btn-sm" value="Go Back" style={{ color:"white",background:"#337ab7",fontSize:"small",float:"right"}} onClick={this.go_back.bind(this)}></input></a> </div>
       
        </div>
        <div className="he" style={{border:"0"}}>
          <div className="head">
            <div> <h6><b>Category {this.props.mem===0?"1 : 1st year B.tech Students":this.props.mem===1?"2 : 2nd year B.tech Students":this.props.mem===2?"3 : 3rd year B.tech Students":this.props.mem===3?"4 : 4th year B.tech Students":this.props.mem===4?" 5 : 1st year M.tech Students":this.props.mem===5?" 6 : 2nd year M.tech Students":this.props.mem===6?" 7 : Others":this.props.mem===7?" 8 : Tutors":this.props.mem===8?" 9 : Staff":null}</b></h6></div>
          </div>
          </div>
          <div className="first_container" >
              <div className="first_container_sub" >
            <div style={{paddingBottom:"3vh"}}> 
              <p style={{color:"black",margin:"0",paddingTop:"2vh"}}>Total Rooms Allocated: <b>{this.props.rooms[this.props.mem].map(dt=>{
                if(dt.length>4){
                  tot=tot+parseInt(dt.slice(4))-parseInt(dt.slice(0,3))
                }else{
                  tot=tot+1
                }
              })}{tot}</b></p>
            <div className="progress">
  <div className="progress-bar" style={{width:(this.props.dt["Category_allocation"]?this.props.dt["Category_allocation"][this.props.mem]["Occupied"]*(100/this.props.dt["Category_allocation"][this.props.mem]["Allocated_rooms"]):null)+"%",backgroundImage:"linear-gradient(to right, #FCA880, #F7CC69)"}}>{(this.props.dt["Category_allocation"]?(this.props.dt["Category_allocation"][this.props.mem]["Occupied"]*(100/this.props.dt["Category_allocation"][this.props.mem]["Allocated_rooms"])+"").slice(0,4):null)+"%"}</div>
</div>
{this.props.mem<6?<p style={{color:"black",margin:"0",paddingTop:"2vh"}}>Batch: <b>{this.props.dt["Category_allocation"]?this.props.dt["Category_allocation"][this.props.mem]["Batch"]:null}</b></p>:null}
                <p style={{color:"black",margin:"0",paddingTop:"2vh"}}>Rooms: <b>{this.props.rooms[this.props.mem].map(dt=>{
                  return("["+dt+"] ")
                })}</b></p>
                  <p style={{color:"black",margin:"0",paddingTop:"2vh"}}>Total Registered Members: <b>{this.props.dt["Category_allocation"]?this.props.dt["Category_allocation"][this.props.mem]["Registered_users"]:null}</b></p>
                 <div style={{display:"flex",flexDirection:"row",paddingTop:"2vh"}}><p style={{color:"black",marginRight:"5%"}}>Occupied : <b>{this.props.dt["Category_allocation"]?this.props.dt["Category_allocation"][this.props.mem]["Occupied"]:null}</b> </p>
              
            <p style={{color:"black"}}>Available : <b>{this.props.dt["Category_allocation"]?tot-this.props.dt["Category_allocation"][this.props.mem]["Occupied"]:null}</b></p>    
           </div>
           <p style={{color:"black",height:"auto"}}>Rooms Occupied :</p>
           <p style={{color:"black",width:"100%"}}> <b>{this.state.room_array}</b></p>  
           <div><a href="#!"><input type="submit" className="btn btn-info btn-sm" value="Vacate all rooms" style={{ color:"white",background:"#a83232",fontSize:"small",float:"right",marginTop:"-2vh"}} onClick={this.show_popup_vacate_room.bind(this)}></input></a></div>
             </div>
              </div>
            </div>
            <div className="he" style={{border:"0"}}>
          <div className="head">
            <div> <h6><b>Room Modification </b></h6></div>
          </div>
          </div>
          <div className="first_container" style={{minHeight:"32vh"}}>
              <div className="first_container_sub" >
            <div> 
  {this.props.rooms[this.props.mem].map(dt=>{
j=j+1;
if(dt.length>4){
  return(
    <div style={{width:"100%",minHeight:"20vh",paddingTop:"2vh"}}>
      <Slider1 shw={j===0?true:false} tmp_rooms={(dt)} no={j} def={[parseInt((dt).slice(0,3)),parseInt((dt).slice(4))]} val={([parseInt((dt).slice(0,3)),parseInt((dt).slice(4))]).slice()} up={([parseInt((dt).slice(0,3)),parseInt((dt).slice(4))]).slice()} change_value_of_room_range={this.change_room_value.bind(this)} del_function={this.show_popup_delete_allocation.bind(this)}/>
      </div>
        )
}else{
  return(
    <div style={{width:"100%",minHeight:"14vh",paddingTop:"6vh"}}>
      <div style={{display:"flex",flexDirection:"row",justifyContent:"space-between"}}>
        <div>
     Room Update: {dt.slice(0,3)}</div>
     <div><a href="#!"><input type="submit" className="btn btn-info btn-sm" value="Delete Allocation" style={{ color:"white",background:"#a83232",fontSize:"small"}} onClick={this.show_popup_delete_allocation.bind(this,dt)}></input></a></div>
      </div>
      </div>
        )
}
   
  })}


         </div>
         
<div style={{marginRight:"1vw",minHeight:"6vh"}}> <a href="#!"><input type="submit" className="btn btn-info btn-sm" value="Set Room Allocation" style={{ color:"white",background:"#337ab7",fontSize:"small",float:"right"}} onClick={this.show_popup_update_cate_alloc.bind(this)}></input></a> </div>
       

              </div>
            </div>
            <div className="he" style={{border:"0"}}>
          <div className="head">
            <div> <h6><b>Unallocated Rooms Table </b></h6></div>
          </div>
          </div>
          <div className="first_container" style={{minHeight:"70vh"}}>
              <div className="first_container_sub" >
            <div> <p style={{color:"black",paddingTop:"2vh",paddingBottom:"4vh",height:"auto",margin:"0"}}>Unallocated Room Table : </p>
            <p style={{width:"100%"}}>The table shows the unallocated rooms avaliable for allocation. If you are interested to add extra rooms to your current category, Click "Select" button to add the corresponding room to the current category room.</p>
    
            <div className="table-responsive text-nowrap" style={{minWidth:"60vw"}}>
        {/* <!--Table--> */}
         <table className="table table-striped " >

          {/* <!--Table head--> */}
          <thead >
            <tr>
            <th class="col-4" style={{fontSize:"0.8rem"}}>#</th>
            <th class="col-4" style={{fontSize:"0.8rem"}}>Rooms Unallocated</th>
      <th class="col-4" style={{fontSize:"0.8rem"}}>Action</th>
            </tr>
          </thead>
          {/* <!--Table head--> */}

          {/* <!--Table body--> */}
          <tbody>
              {this.state.list?this.state.list.map(dt=>{
                i=i+1
                  return(
              <tr>
              <th class="col-4" style={{fontSize:"0.8rem"}}>{i}</th>
              <td class="col-4" style={{fontSize:"0.8rem"}}>{dt}</td>
              <td class="col-4" style={{fontSize:"0.8rem"}}><a href="#!"><input type="submit" className="btn btn-info btn-sm" value="Select" style={{ color:"white",background:"#337ab7",fontSize:"small"}} onClick={this.add_room.bind(this,i-1)}></input></a></td>
              </tr>
                  )

              }):<tr>
              <th class="col-4" style={{fontSize:"0.8rem"}}>0</th>
              <td class="col-4" style={{fontSize:"0.8rem"}}>No Rooms Available</td>
              <td class="col-4" style={{fontSize:"0.8rem"}}><a href="#!"><input type="submit" className="btn btn-info btn-sm" value="Select" style={{ color:"white",background:"#337ab7",fontSize:"small"}}></input></a>
              </td>
              </tr>}
              
          </tbody>
          {/* <!--Table body--> */}


        </table>
        {/* <!--Table--> */}
      </div>
      <div style={{width:"100%",margin:"0",padding:"2vh 0"}}>
        {this.state.show_sub_slider?
      <Slider2  info={this.state.slider_info} uproom={this.update_room_for_spec.bind(this)} uproom_spec={this.update_room_special.bind(this)}/>:null}
      </div>
      {/* {this.state.show_sub_slider?:null} */}
            </div>
            </div>
            </div>
        </div>
    )
    
  }
}
let dt=[10,20,40,60]
class Slider2 extends Component{
  constructor(props){
    super(props)
    this.state ={
      defaultValues:dt,
      values: dt.slice(),
      update: dt.slice(),
      list:[],
      domain:[0,100]
    } 
  }
  componentDidMount(){
    let arr =[parseInt(this.props.info[0].slice(0,3)),parseInt(this.props.info[0].slice(4))]
    this.setState({
      domain:arr,
      values:arr.slice(),
      update:arr.slice(),
      temp:parseInt(this.props.info[0].slice(0,3))
    })
  }
  onUpdate = update => {function getZero(n){
    let num=n+""
    if(num.length===1){
      return "00"+n
    }else if(num.length===2){
      return "0"+n
    }else{
      return n+""
    }
  }
  let ab=getZero(update[0])+"-"+getZero(update[1])

    this.setState({ update:update,
      })      
    }
    
    room_add_for_spec_cati(msg){
      if(msg===0){
        this.props.uproom_spec(this.state.temp)
      }else{
        this.props.uproom(this.state.update)
      }
    }
  render(){
    return(<div>
      {this.props.info[0].length<4?<div style={{display:'flex',flexDirection:"row",justifyContent:"space-between"}}>
        <div> <p style={{color:"black",margin:"0",paddingBottom:"4vh"}}>Add Rooms : {this.state.temp}</p></div>
        <div >
        <a href="#!"><input type="submit" className="btn btn-info btn-sm" value="Add & Exit" style={{ color:"white",background:"#337ab7",fontSize:"small",float:"right"}} onClick={this.room_add_for_spec_cati.bind(this,0)}></input></a>
   </div>
      </div>: <div> <div>
            <div> <p style={{color:"black",margin:"0",paddingBottom:"4vh"}}>Add Rooms : {this.state.update[0]}-{this.state.update[1]}</p></div>
<Slider
          mode={3}
          step={1}
          domain={this.state.domain}
          rootStyle={sliderStyle}
          onUpdate={this.onUpdate}
          onChange={this.onChange}
          values={this.state.values}
        >
          <Rail>
            {({ getRailProps }) => <SliderRail getRailProps={getRailProps} />}
          </Rail>
          <Handles >
            {({ handles, getHandleProps }) => (
              <div className="slider-handles">
                {handles.map(handle => (
                  <KeyboardHandle
                    key={handle.id}
                    handle={handle}
                    domain={[0,100]}
                    getHandleProps={getHandleProps}
                  />
                ))}
              </div>
            )}
          </Handles>
          <Tracks left={false} right={false}>
            {({ tracks, getTrackProps }) => (
              <div className="slider-tracks">
                {tracks.map(({ id, source, target }) => (
                  <Track
                    key={id}
                    source={source}
                    target={target}
                    getTrackProps={getTrackProps}
                  />
                ))}
              </div>
            )}
          </Tracks>
          <Ticks count={10}>
            {({ ticks }) => (
              <div className="slider-ticks">
                {ticks.map(tick => (
                  <Tick key={tick.id} tick={tick} count={ticks.length} />
                ))}
              </div>
            )}
          </Ticks>
        </Slider>

      </div> 
      <div style={{paddingTop:"6vh"}}>
        <a href="#!"><input type="submit" className="btn btn-info btn-sm" value="Add & Exit" style={{ color:"white",background:"#337ab7",fontSize:"small",float:"right"}} onClick={this.room_add_for_spec_cati.bind(this,1)}></input></a>
   </div></div>}
    
      </div>)
  }
}
// General Slider class
class Slider1 extends Component{
  constructor(props){
    super(props)
    this.state ={
      defaultValues:[],
      values: null,
      update: null,
      list:[],
      domain:[0,100],
      temp_rooms:"",
      key:-1
    } 
  }
  componentDidMount(){
    this.setState({
      temp_rooms:this.props.tmp_rooms,
      domain:this.props.def,
      values:this.props.val,
      update:this.props.up,
      key:this.props.no
    })
  }

   getZero(n){
    let num=n+""
    if(num.length===1){
      return "00"+n
    }else if(num.length===2){
      return "0"+n
    }else{
      return n+""
    }
  }
  set_room_alloc_2(){
    this.props.del_function(this.state.temp_rooms)
  }
  onUpdate = update => {
  let ab=this.getZero(update[0])+"-"+this.getZero(update[1])

    this.setState({ update:update,
      temp_rooms:ab})  
      this.props.change_value_of_room_range(this.state.key,ab)    
    }
    onChange = values => {
      this.setState({ values })
      
    }
  render(){
    return(<div>
      <div style={{paddingBottom:"3vh"}}>
        <div style={{display:"flex",flexDirection:"row",justifyContent:"space-between"}}>
      <p style={{color:"black",margin:"0"}}>Room Update:  {this.state.temp_rooms}</p>
      {this.state.key!=0?<div><a href="#!"><input type="submit" className="btn btn-info btn-sm" value="Delete Allocation" style={{ color:"white",background:"#a83232",fontSize:"small"}} onClick={this.set_room_alloc_2.bind(this)}></input></a></div>:null}
      </div>
        {this.props.shw? <p style={{width:"100%"}}>Warning:: Deallocation in the occupied will automatically delete all the occupied users in the rooms.</p>
       :null}</div>
      <Slider
          mode={3}
          step={1}
          domain={this.state.domain}
          rootStyle={sliderStyle}
          onUpdate={this.onUpdate}
          onChange={this.onChange}
          values={this.state.values}
        >
          <Rail>
            {({ getRailProps }) => <SliderRail getRailProps={getRailProps} />}
          </Rail>
          <Handles >
            {({ handles, getHandleProps }) => (
              <div className="slider-handles">
                {handles.map(handle => (
                  <KeyboardHandle
                    key={handle.id}
                    handle={handle}
                    domain={this.state.domain}
                    getHandleProps={getHandleProps}
                  />
                ))}
              </div>
            )}
          </Handles>
          <Tracks left={false} right={false}>
            {({ tracks, getTrackProps }) => (
              <div className="slider-tracks">
                {tracks.map(({ id, source, target }) => (
                  <Track
                    key={id}
                    source={source}
                    target={target}
                    getTrackProps={getTrackProps}
                  />
                ))}
              </div>
            )}
          </Tracks>
          <Ticks count={10}>
            {({ ticks }) => (
              <div className="slider-ticks">
                {ticks.map(tick => (
                  <Tick key={tick.id} tick={tick} count={ticks.length} />
                ))}
              </div>
            )}
          </Ticks>
        </Slider>
</div>)
  }
}
// Change the range of Values-code
export function ValueViewer() {
  return (
    <div>
      {this.values}
    </div>
  )
} 

export function SliderRail({ getRailProps }) {
  return (
    <Fragment>
      <div style={railOuterStyle} {...getRailProps()} />
      <div style={railInnerStyle} />
    </Fragment>
  )
}

SliderRail.propTypes = {
  getRailProps: PropTypes.func.isRequired,
}

// *******************************************************
// HANDLE COMPONENT
// *******************************************************
export function Handle({
  domain: [min, max],
  handle: { id, value, percent },
  disabled,
  getHandleProps,
}) {
  return (
    <Fragment>
      <div
        style={{
          left: `${percent}%`,
          position: 'absolute',
          transform: 'translate(-50%, -50%)',
          WebkitTapHighlightColor: 'rgba(0,0,0,0)',
          zIndex: 5,
          width: 28,
          height: 42,
          cursor: 'pointer',
          // border: '1px solid white',
          backgroundColor: 'none',
        }}
        {...getHandleProps(id)}
      />
      <div
        role="slider"
        aria-valuemin={min}
        aria-valuemax={max}
        aria-valuenow={value}
        style={{
          left: `${percent}%`,
          position: 'absolute',
          transform: 'translate(-50%, -50%)',
          zIndex: 2,
          width: 24,
          height: 24,
          borderRadius: '50%',
          boxShadow: '1px 1px 1px 1px rgba(0, 0, 0, 0.3)',
          backgroundColor: disabled ? '#666' : 'white',
        }}
      />
    </Fragment>
  )
}

Handle.propTypes = {
  domain: PropTypes.array.isRequired,
  handle: PropTypes.shape({
    id: PropTypes.string.isRequired,
    value: PropTypes.number.isRequired,
    percent: PropTypes.number.isRequired,
  }).isRequired,
  getHandleProps: PropTypes.func.isRequired,
  disabled: PropTypes.bool,
}

Handle.defaultProps = {
  disabled: false,
}

// *******************************************************
// KEYBOARD HANDLE COMPONENT
// Uses a button to allow keyboard events
// *******************************************************
export function KeyboardHandle({
  domain: [min, max],
  handle: { id, value, percent },
  disabled,
  getHandleProps,
}) {
  return (
    <button
      className="keyboard_style"
      role="slider"
      aria-valuemin={min}
      aria-valuemax={max}
      aria-valuenow={value}
      style={{
        left: `${percent}%`,
        position: 'absolute',
        transform: 'translate(-50%, -50%)',
        zIndex: 2,
        width: 24,
        height: 24,
        borderRadius: '50%',
        boxShadow: 'inset  10px -10px 25px rgba(0,0,0,0.6)',
        backgroundColor :'white',
      }}
      {...getHandleProps(id)}
    />
  )
}

KeyboardHandle.propTypes = {
  domain: PropTypes.array.isRequired,
  handle: PropTypes.shape({
    id: PropTypes.string.isRequired,
    value: PropTypes.number.isRequired,
    percent: PropTypes.number.isRequired,
  }).isRequired,
  getHandleProps: PropTypes.func.isRequired,
  disabled: PropTypes.bool,
}

KeyboardHandle.defaultProps = {
  disabled: false,
}

// *******************************************************
// TRACK COMPONENT
// *******************************************************
export function Track({ source, target, getTrackProps, disabled }) {
  return (
    <div
      style={{
        position: 'absolute',
        transform: 'translate(0%, -50%)',
        height: 14,
        zIndex: 1,
        backgroundColor: disabled ? 'white' : 'white',
        borderRadius: 7,
        cursor: 'pointer',
        left: `${source.percent}%`,
        width: `${target.percent - source.percent}%`,
      }}
      {...getTrackProps()}
    />
  )
}

Track.propTypes = {
  source: PropTypes.shape({
    id: PropTypes.string.isRequired,
    value: PropTypes.number.isRequired,
    percent: PropTypes.number.isRequired,
  }).isRequired,
  target: PropTypes.shape({
    id: PropTypes.string.isRequired,
    value: PropTypes.number.isRequired,
    percent: PropTypes.number.isRequired,
  }).isRequired,
  getTrackProps: PropTypes.func.isRequired,
  disabled: PropTypes.bool,
}

Track.defaultProps = {
  disabled: false,
}

// *******************************************************
// TICK COMPONENT
// *******************************************************
export function Tick({ tick, count, format }) {
  return (
    <div>
      <div
        style={{
          position: 'absolute',
          marginTop: 14,
          width: 1,
          height: 5,
          backgroundColor: 'rgb(200,200,200)',
          left: `${tick.percent}%`,
        }}
      />
      <div
        style={{
          position: 'absolute',
          marginTop: 22,
          fontSize: 10,
          textAlign: 'center',
          marginLeft: `${-(100 / count) / 2}%`,
          width: `${100 / count}%`,
          left: `${tick.percent}%`,
        }}
      >
        {format(tick.value)}
      </div>
    </div>
  )
}

Tick.propTypes = {
  tick: PropTypes.shape({
    id: PropTypes.string.isRequired,
    value: PropTypes.number.isRequired,
    percent: PropTypes.number.isRequired,
  }).isRequired,
  count: PropTypes.number.isRequired,
  format: PropTypes.func.isRequired,
}

Tick.defaultProps = {
  format: d => d,
}
export default Edit_category