     async fetch_data(){
      try {
        await axios.get('https://simple.cijomanuel.repl.co/api/data_2')
                    .then((response)=>{
                          // handle success
                          let data = response.data;
                            let v1=data["Category_allocation"][0]["Allocated_rooms"],v2=data["Category_allocation"][1]["Allocated_rooms"],v3=data["Category_allocation"][2]["Allocated_rooms"],v4=data["Category_allocation"][3]["Allocated_rooms"];
                          let m1=data["Category_allocation"][4]["Allocated_rooms"],m2=data["Category_allocation"][5]["Allocated_rooms"],m3=data["Category_allocation"][6]["Allocated_rooms"],m4=data["Category_allocation"][7]["Allocated_rooms"],m5=data["Category_allocation"][8]["Allocated_rooms"];
                          let dt=[v1,(v1+v2),(v1+v2+v3),(v1+v2+v3+v4),(v1+v2+v3+v4+m1),(v1+v2+v3+v4+m1+m2),(v1+v2+v3+v4+m1+m2+m3),(v1+v2+v3+v4+m1+m2+m3+m4),(v1+v2+v3+v4+m1+m2+m3+m4+m5)]
                          function getZero(n){
                            let num=n+""
                            if(num.length===1){
                              return "00"+n
                            }else if(num.length===2){
                              return "0"+n
                            }else{
                              return n+""
                            }
                          }
                          let ba=[data["Category_allocation"][0]["Room_no"],data["Category_allocation"][1]["Room_no"],data["Category_allocation"][2]["Room_no"],data["Category_allocation"][3]["Room_no"],data["Category_allocation"][4]["Room_no"],data["Category_allocation"][5]["Room_no"],data["Category_allocation"][6]["Room_no"],data["Category_allocation"][7]["Room_no"],data["Category_allocation"][8]["Room_no"]]
                          let ab=["001-"+getZero(dt[0]),getZero(dt[0]+1)+"-"+getZero(dt[1]),getZero(dt[1]+1)+"-"+getZero(dt[2]),getZero(dt[2]+1)+"-"+getZero(dt[3]),getZero(dt[3]+1)+"-"+getZero(dt[4]),getZero(dt[4]+1)+"-"+getZero(dt[5]),getZero(dt[5]+1)+"-"+getZero(dt[6]),getZero(dt[6]+1)+"-"+getZero(dt[7]),getZero(dt[7]+1)+"-"+getZero(dt[8])]
                          this.setState({
                            slider_domain:[0,data["Total_room"]],
                            defaultValues:dt,
                            values: dt.slice(),
                            update: dt.slice(),
                            rooms:ab,
                            dt:data,
                            total_room_1:data["Total_room"],
                            total_room_2:data["Total_room"],
                            room_allocation_mode_1:data["Mode"],
                            room_allocation_mode_2:data["Mode"],
                            occupied_percentage:(data["Occupied"]*(100/data["Total_room"])+"").slice(0,2)+"%",
                            allocated_1:data["Allocated"],
                            allocated_2:data["Allocated"],
                            unallocated_rooms:146,
                            unallocated_list:data["Unallocated_list"][0]["list"],
                            room_no_array:ba,
                            server_response:"success",
                            delete_search_load:true
                          })
                          })

        
      } catch (error) {
        this.setState({server_response:"fail"})
        console.error(error);
      }
      
    }
