          <div style={{paddingTop:"1.5vh",height:"100%", marginRight:"1vw"}} className="Attentence_menu_button"> <a href="#"><input type="submit" className="btn btn-info btn-sm" value="Attendence Menu" style={{ color:"white",background:"#337ab7",fontSize:"small",float:"right"}} onClick={this.show_attendence_menu.bind(this)}></input></a> </div>
