import React from 'react';
import {Bar} from 'react-chartjs-2';

export default class Bargraph extends React.Component {
    constructor(){
        super()
        this.state={
            state : {
                labels: [],
                datasets: [
                 
                ]
              },
            date:""
        }
    }
    componentDidMount(){
        this.setState({
            state : {
                labels: ['All', 'Students', 'Tutors',
                'Others', 'Staff'],
                datasets: [
                    {
                        label: 'Hostel Attendence',
                        backgroundColor: '#1048A0',
                        borderColor: 'rgba(0,0,0,1)',
                        borderWidth: 2,
                        data: this.props.data["hostel"]
                      },
                      {
                          label: 'Mess Attendence',
                          backgroundColor: '#E5E7F4',
                          borderColor: '#E5E7F4',
                          borderWidth: 2,
                          data: this.props.data["mess"]
                        }
                ]
              }
        })
    }
    componentDidUpdate(){
        
    }
  render() {
//       if(this.state.state.labels===[]){
//     return(
//         <div>Accessing Data....</div>
//     )
// }else{
    return (
      <div>
        <Bar
          data={this.state.state}
          options={{
            legend:{
              display:true,
              position:'right'
            }
          }}
        />
      </div>
    );
  }
}
