import react from "react";
import './style_input_attendance.scss';
import Checkbox from '@material-ui/core/Checkbox';
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';

import { makeStyles } from '@material-ui/core/styles';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';
import Button from '@material-ui/core/Button';

import SearchIcon from '@material-ui/icons/Search';

import Table from './attendance_table'
// import { flexibleCompare } from "@fullcalendar/react";

import data from "./input_attendance.json";

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {faChalkboardTeacher,faSuitcase,faUserGraduate,faTable } from '@fortawesome/free-solid-svg-icons'




 class InputAttendance extends react.Component{


        constructor(){
            super()

                this.state={
                    input_page:true,
                    button:"SUBMIT",
                    filter:"none",
                    filter_radio:"",
                    filter_checkbox:"",
                    search:false
                  
                    
                    
                    
                }   
             this.input_page = this.input_page.bind(this);
             this.filter = this.filter.bind(this);
             this.filter_radiobox = this.filter_radiobox.bind(this);
             this.filter_checkbox = this.filter_checkbox.bind(this);
            //  this. slider = this. slider.bind(this);
            //  this. valuetext = this. valuetext.bind(this);
            //  this.AddPresentButton = this.AddPresentButton.bind(this);
            //  this.AddAbsentButton = this.AddAbsentButton.bind(this);
            //  this.AddViewButton = this.AddViewButton.bind(this);
            //  this.AddRemoveButton = this.AddRemoveButton.bind(this);
             this.search = this.search.bind(this);

            
             
             
        }

      

        componentDidMount(){
            
          const isMobile = window.matchMedia("only screen and (max-width: 760px)").matches;

          if(isMobile){
            alert("switch to landscape mode");
          }
            
           
          
        }
  
        input_page(){
            this.state.input_page==true ? this.setState({button:"INPUT ATTENDANCE"}) : this.setState({button:"SUBMIT"})
            this.setState({
                input_page:!this.state.input_page
            })
            
            
        }
        filter(e){
          console.log(e.target.value)
            this.setState({
                filter:e.target.value,

            })
           
              
            
            
        }
        search(){
          this.setState({
            search:false
          })
          this.setState({
            search:true
          })
          
        }
        filter_radiobox(e){
            console.log(e.target.value)
            this.setState({
              filter_radio:e.target.value
            })
            
        }

        filter_checkbox(e){
            // console.log(e.target.value)
            // console.log(e.target.checked)
            this.setState({
              filter_checkbox:e.target.value
            })
        }

        // slider(event, newValue){
        //   this.setState({
        //     current_value:newValue
        //   })
        // }

        // valuetext(value) {
        //   return value;
        // }

//           AddPresentButton = (e) =>{
            
//             console.log(e.target)
//   } 
//   AddAbsentButton = (e) =>{
//     console.log(e.target)
    
//   } 
//   AddViewButton = (e) =>{
//     // console.log(e.target)
   
//   } 

// AddRemoveButton = (e) =>{
//   console.log(e.target)
// } 




        render(){
            const today= new Date();
            const date = today.getDate()+"-"+(today.getMonth()+1)+"-"+today.getFullYear();

            
            
          
         
            
            return(
                <div id='P2' className="p2" >
      <div className="he">
          <div className="head">
            <div> <h5 ><b>Input Attendence</b></h5></div>
          </div>
        </div>
        {/* coding starts */}
        

            <div class="input_header">
               <div  class="div1"><center><strong>{date}</strong></center></div>
               <div style={{paddingTop:"1.5vh", marginRight:"1vw"}}> <a href="#"> <input type="submit" className="btn btn-info btn-sm" value={this.state.button} style={{ color:"white",background:"#337ab7",fontSize:"small",float:"right"}}onClick={this.input_page} ></input></a></div>
            </div>

            {this.state.input_page==true ? 
            <div >
               
               <div class="filter_box" style={{marginTop:"6vh"}}>
            <div className="row" style={{margin:"0",paddingTop:"1vh",paddingLeft:"2vw"}}><h6><b>Filter Box</b></h6></div>
                <div >
                
               <div class="search">
               <div className="row" style={{margin:"0",paddingTop:"1vh",paddingLeft:"5vw"}}>
            <div className="form-group form_width3" style={{width:"40%"}}>
  <form action="" >
            <label for="exampleInputSelection"><b>Choose Selection Mode: </b></label>
            <select name="mode_selection" id="mode_selection" className="form-control" onChange={this.filter}>
              
            <option value="none">None</option>
            <option value="student">Student</option>
            <option value="tutor">Tutor</option>
            <option value="staff">Staff</option>
            <option value="room">Room</option>
            </select>
            </form>
  </div>
  
  {/* <div className="form-group form_width3" style={{width:"40%",display:"flex",flexDirection:"row-reverse"}}>
  <Button
         variant="contained"
        startIcon={<SearchIcon />}
        size="small"
        style={{backgroundColor:"#337ab7",color:'white',height:"30px",marginTop:"10%"}}
        onClick={this.search}
      >
        Search
      </Button>
  </div> */}
  </div>
               

      
      </div>

            {this.state.filter=="student" &&
            <div class="filter" style={{marginLeft:"3%"}}>
            <FormControl component="fieldset" >
            <label for="exampleInputSelection"><b>Choose Classification: </b></label>
            
      <RadioGroup row aria-label="position" name="position" defaultValue="top"onClick={this.filter_radiobox}>
      <FormControlLabel value="BTECH" control={<Radio color="primary" />} label="BTECH"  />
        <FormControlLabel value="MBA" control={<Radio color="primary" />} label="MBA"  />
        <FormControlLabel value="ALL" control={<Radio color="primary" />} label="ALL"  />
      </RadioGroup>
    </FormControl>

    {this.state.filter_radio=="BTECH" &&
      <FormControl component="fieldset">
        <label for="exampleInputSelection"><b>Choose Batch Year: </b></label>
            
      <FormGroup aria-label="position" row  onClick={this.filter_checkbox}>
        
        <FormControlLabel
          value="First"
          control={<Checkbox color="primary" />}
          label="First year"
          labelPlacement="end"
        />
        <FormControlLabel
          value="Second"
          control={<Checkbox color="primary" />}
          label="Second year"
          labelPlacement="end"
        />
        
        <FormControlLabel
          value="Third"
          control={<Checkbox color="primary" />}
          label="Third year"
        />
        <FormControlLabel
          value="Fourth"
          control={<Checkbox color="primary" />}
          label="Fourth year"
          labelPlacement="end"
        />
        
      </FormGroup>
    </FormControl>}
    {this.state.filter_radio=="MBA" &&
    <FormControl component="fieldset">
    <FormGroup aria-label="position" row  onClick={this.filter_checkbox}>
      
      <FormControlLabel
        value="First"
        control={<Checkbox color="primary" />}
        label="First year"
        labelPlacement="end"
      />
      <FormControlLabel
        value="Second"
        control={<Checkbox color="primary" />}
        label="Second year"
        labelPlacement="end"
      />
      
     
      
    </FormGroup>
  </FormControl>
  
        }
    

    </div>
        }
    

       
   
     
            </div>
            <div className="row" style={{margin:"0",paddingTop:"1vh",paddingLeft:"5vw"}}>
            
                <div className="form-group " style={{width:"90%",display:"flex",flexDirection:"row-reverse"}}>
  <Button
         variant="contained"
        startIcon={<SearchIcon />}
        size="small"
        style={{backgroundColor:"#337ab7",color:'white',height:"30px"}}
        onClick={this.search}
      >
        Search
      </Button>
  </div></div>
        </div>
           
           
          

        {(this.state.search==true) &&(this.state.filter=="student" || this.state.filter=="tutor" || this.state.filter=="staff" ||this.state.filter=="room") &&
   <div className="row" style={{margin:"0",paddingTop:"1vh",paddingLeft:"2vw",marginLeft:"5%"}}><h6><b>Attendance List</b></h6></div>
            
   }
   {(this.state.search==true && this.state.filter_checkbox!=""&&this.state.filter=="student") && 
   
   <Table data={data.student}/>
   }
   {this.state.search==true &&this.state.filter=="tutor" &&
   <Table data={data.tutor}/>
   } 
    
   {this.state.search==true &&this.state.filter=="staff" &&
   <Table data={data.staff}/>
   }
   {this.state.search==true &&this.state.filter=="room" &&
   <Table data={data.student}/>
   }
    
    
           </div>
            :
            <div className="view_absentees">
              
              <div className="absentees_details">
                <div className="block"style={{backgroundColor:"#DAF7A6"}} >
                <div style={{marginLeft:"1vw"}}>145/150</div>
                <div style={{marginLeft:"1vw"}}>Total</div>
                  <div className="icons">
                  <FontAwesomeIcon icon={faTable} className="icon_style fa-2x" aria-hidden="true"  />
                </div>
                <div style={{width:"100%",height:"1.5vh",backgroundColor:"#aaaaad"}}></div>
                  </div>
                <div className="block"style={{backgroundColor:"#FFC300"}} >
                <div style={{marginLeft:"1vw"}}>75/100</div>
                <div style={{marginLeft:"1vw"}}> Student</div>
                  <div className="icons">
                <FontAwesomeIcon icon={faUserGraduate} className="icon_style fa-2x" aria-hidden="true" />
                </div>
                <div style={{width:"100%",height:"1.5vh",backgroundColor:"#aaaaad"}}></div>
                  </div>
                <div className="block"style={{backgroundColor:"#FF5733"}} >
                <div style={{marginLeft:"1vw"}}>75/100</div>
                <div style={{marginLeft:"1vw"}}>Tutor</div>
                  
                  <div className="icons">
                <FontAwesomeIcon icon={faChalkboardTeacher} className="icon_style fa-2x" aria-hidden="true" />
                </div>
                <div style={{width:"100%",height:"1.5vh",backgroundColor:"#aaaaad"}}></div>
                  </div>
                <div className="block" style={{backgroundColor:"#C70039"}}>
                <div style={{marginLeft:"1vw"}}>75/100</div>
                <div style={{marginLeft:"1vw"}}>Staff</div>
                  <div className="icons">
                  <FontAwesomeIcon icon={faSuitcase} className="icon_style fa-2x" aria-hidden="true" />
                </div>
                <div style={{width:"100%",height:"1.5vh",backgroundColor:"#aaaaad"}}></div>
                  </div>
              </div>
                <div style={{paddingTop:"3vh"}}><center><strong>Absentees List</strong></center></div>
              <Table data={data.student}/>
              
              </div> }
            
           

            <div>
            
            </div>

            

        {/* coding ends */}
      </div>
    
    

            )
                }
       
}

export default InputAttendance;