import React, { Component } from 'react'
import QrReader from 'react-qr-reader'
import './style_authentication.scss'
import Audio from "./sounds/alert.mp3"
import Entry_data from "./Entry_exit_data.json"
import ReactCardFlip from 'react-card-flip';
import Room_data_select from "./Select_room_data.json"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faSearch } from '@fortawesome/free-solid-svg-icons'
class Qrread extends Component {
  state = {
    modal_box:0,
    isFlipped:false,
    entry_exit_data:[],
    target_data:{},
    select_room_data:[],
    selected_room:"Select any room",
    selected_room_slot:"Select Room Slot",
    swap_person:"",
    present_date:"",
    flipside_conten:"room_selection",
    modal_search_clear:false,
    show:true
    }
componentDidMount(){
  var today = new Date();
      var dd = String(today.getDate()).padStart(2, '0');
      var mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!
      var yyyy = today.getFullYear();
      
      today = mm + '/' + dd + '/' + yyyy;
      this.setState({
        present_date:today
      })
  // The following statements must be replaced in backend.Currently it's used for demonstration
  this.setState({
    entry_exit_data:Entry_data["data"]
  })
}
confirm(msg){
  if(msg===1){
    this.setState({
      modal_box:msg
    })
  }else{
    if( document.getElementById("myModal5").style.display = "block"){
        document.getElementById("myModal5").style.display = "none";
      } 
      
    this.props.call();
  }
}
show_msg(){
  alert("Please Select a Room.");
}
bt(i){
  if(i===2){
    this.setState({
      isFlipped:false
    })
  }else{
    if( document.getElementById("myModal8").style.display = "block"){
        document.getElementById("myModal8").style.display = "none";
      } 
      this.setState({
        modal_box:0,
        isFlipped:false
      })
      this.props.call();
  }
}
handleClick(msg) {
  this.setState(prevState => ({ isFlipped: !prevState.isFlipped }));
  this.setState({
    flipside_content:msg
  })
}
clear_data_function(){
  this.setState({
    entry_exit_data:[{"name":"no_data","email":"no_data","mobile_number":["null","null"],"status":"no_data"}]
  })
}
clear_search(){
  this.setState({
    select_room_data:Room_data_select["data"],
    modal_search_clear:false,
    selected_room:"Select any room",
    selected_room_slot:"Select Room Slot"
  })
}
  handleScan = data => {
    
    // The following statements must be replaced in backend.Currently it's used for demonstration
   let arr= this.state.entry_exit_data
    if (data) {
      let reg_num=data.substr(0,15)
      let name=data.substr(16)
      arr.map(dt=>{
        if(dt.Reg_number===reg_num && dt.name===name){
          this.setState({
            target_data:dt
          })
          if( document.getElementById("myModal8").style.display = "none"){
            document.getElementById("myModal8").style.display = "block";
           document.getElementById('audio').play()
           this.setState({
            select_room_data:Room_data_select["data"],
            selected_individual_data:Room_data_select,
            show:false
           })
          }
          
        }
      })

    }
    
  }
  find(){
    // this setState to update select_room_data must be updated in backend 
    let ab = document.getElementById("inptxt").value,no=0;
    if(parseInt(ab)){
      let arr= this.state.select_room_data,array =[];
      arr.map(dt=>{
        if(parseInt(dt["Room_number"])===parseInt(ab)){
         array.push(dt)
         this.setState({
          select_room_data:array
        })
          no=1;
        }
      })

      if(no===0){
        this.setState({
        select_room_data:[{"Room_number":"no_data","Roommates":["no_data","no_data","no_data"]}]
      })
      }
      this.setState({
        modal_search_clear:true
      })

    }
  }
  room_select(msg,event){
    let arr= this.state.select_room_data,num=0
    if(event.target.name==="slot B"){
      num=1;
    }else if(event.target.name==="slot C"){
      num=2;
    }
    this.setState({
      selected_room:arr[msg]["Room_number"],
      selected_room_slot:event.target.name,
      swap_person:arr[msg]["Roommates"][num]
    })
  }
  handleError = err => {
    console.error(err)
  }
  render() {
   let i=0;
    return (
      <div className="qr">
         <audio id="audio"><source src={Audio} type="audio/mp3"></source></audio>
         <div id="myModal8" className="modal">
              {this.state.modal_box===0?
<ReactCardFlip isFlipped={this.state.isFlipped} flipDirection="vertical">
<div>
  <div className="modal-content" id="modal_con2">
  <div className="row" style={{margin:"0"}}>
  <div className="col modal_col" >
      <img src={this.state.target_data["img"]} alt='the profile picture' className="img_modal"/> 
  </div>
  <div className="col modal_col modal_col_extra" style={{height:"auto"}}>
                  <a href="#!" style={{textDecoration:"numne"}}><span className="close" id='cls' onClick={this.bt.bind(this)} >&times;</span></a>  
      <div  style={{fontFamily:"Roboto, sans-serif"}} className="sp2">
        <center><h2 style={{fontWeight:"bold"}} className="modal_head_fontcap">{this.state.target_data["name"]}</h2>
        <p  className="modal_head_font">Email: {this.state.target_data["email"]}</p></center>
      </div>
      <div  style={{fontFamily:"Roboto, sans-serif"}} className="sp2">
      <center>
        <p  className="modal_head_font2" style={{color:"black",marginBottom:"0"}}>Status: {this.state.target_data["status"]}
      </p>
              {this.state.target_data["status"]==="Occupied"?<p  className="modal_head_font2" style={{color:"black",marginBottom:"0"}}>Room Number: {this.state.target_data["room_no"]}</p>:null}
              {this.state.target_data["status"]==="Active"?<p  className="modal_head_font2" style={{color:"black",marginBottom:"0"}}>Previous Room Number: {this.state.target_data["Previous_rooms"].map(dt=>{
                return("["+dt+"]")
              })}</p>:null}
              <p  className="modal_head_font2" style={{color:"black",marginBottom:"0"}}>Mobile Number 1: {this.state.target_data["mobile_number"]?this.state.target_data["mobile_number"][0]:null}</p>
              <p  className="modal_head_font2" style={{color:"black",marginBottom:"0"}}>Mobile Number 2: {this.state.target_data["mobile_number"]?this.state.target_data["mobile_number"][1]:null}</p>
              
              {this.state.target_data["status"]==="Occupied"?
              <div><div style={{marginTop:"2vh"}}>
              <a href="#!" class="btn btn-primary btn-lg active" role="button" aria-pressed="true"  className="modal_button_font"  onClick={this.handleClick.bind(this,"vacate_room")}>Vacate Room</a>
              </div><div style={{marginTop:"4vh"}}>
              <a href="#!" class="btn btn-primary btn-lg active" role="button" aria-pressed="true"  className="modal_button_font"  onClick={this.handleClick.bind(this,"swap_room")}>Swap Room</a>
              </div></div>:this.state.target_data["status"]==="Active"?<div style={{marginTop:"4vh"}}>
              <a href="#!" class="btn btn-primary btn-lg active" role="button" aria-pressed="true"  className="modal_button_font"  onClick={this.handleClick.bind(this,"room_selection")}>Select Room</a>
              </div>:null}
              </center>
      </div>
  </div>
</div>
</div></div>
<div>
  {(this.state.flipside_content==="room_selection" || this.state.flipside_content==="swap_room")?
  <div className="modal-content modal_extra" id="modal_con" >
  <div className="attend_info_row2" style={{margin:"0"}} >
    <div className="row" style={{width:"100%"}}>
  <div className="row" style={{margin:"0 5%",height:"5vh",width:"80%",justifyContent:"center"}} >
    {this.state.flipside_conten==="room_selection"?
   <h5 style={{fontWeight:"bold",marginTop:"1vh"}} >Room Selection</h5>:
   <h5 style={{fontWeight:"bold",marginTop:"1vh"}} >Swap Room Selection</h5>}
  </div>
  <div style={{width:"5%"}}>
  <a href="#!" style={{textDecoration:"none"}}><span className="close" id='cls' onClick={this.bt.bind(this,0)} >&times;</span></a>  
  </div>
  </div>
  <div className="row" style={{width:"100%",margin:"0"}}>
    {this.state.flipside_content==="swap_room"?<div className="row" style={{display:"flex",flexDirection:"row",margin:"1vh 5%",width:"100%"}}>
    <div className="col" style={{width:"50%",display:"flex",flexDirection:"row"}}>
    <p  style={{color:"black",marginBottom:"0"}}>Current Room Number :</p> <p>{this.state.target_data["room_no"]}</p></div>
    
  </div>:null}
  <div className="row" style={{display:"flex",flexDirection:"row",margin:"0.3vh 5%",width:"100%"}}>
    <div className="col" style={{width:"50%",display:"flex",flexDirection:"row"}}>
    <p  style={{color:"black",marginBottom:"0"}}>Room Number :</p> <p>{this.state.selected_room}</p></div>
    <div className="col" style={{width:"50%",display:"flex",flexDirection:"row"}}>
      <p  style={{color:"black",marginBottom:"0"}}>Slot Number : </p><p> {this.state.selected_room_slot}</p>
    </div>
  </div>
  <div className="row" style={{display:"flex",flexDirection:"row",margin:"0.3vh 5%",width:"100%"}}>
    <div className="col" style={{width:"50%",display:"flex",flexDirection:"row"}}>
    <p  style={{color:"black",marginBottom:"0"}}>Batch : {this.state.selected_individual_data["Batch"]}</p></div>
    <div className="col" style={{width:"50%",display:"flex",flexDirection:"row"}}>
      <p  style={{color:"black",marginBottom:"0"}}>Allocated Rooms :{this.state.selected_individual_data["Allocated_Rooms"]?this.state.selected_individual_data["Allocated_Rooms"].map(dt=>{
        return("["+dt+"]")
      }):null}</p>
    </div>
  </div>
  <div className="row" style={{display:"flex",flexDirection:"row",margin:"1vh 0",width:"100%",height:"4vh",borderTop:"1px solid #ECEEF0"}}>
              <div style={{width:"60%"}}></div>
              <div><input type="text" placeholder="Search by Room Number" className="form-control" name="search" id="inptxt" style={{height:"3vh",fontSize:"0.7rem",marginLeft:"2vw",marginTop:"1vh"}}/>
              </div>
              <a href="#!" style={{marginTop:"1vh"}}><FontAwesomeIcon icon={faSearch}  onClick={this.find.bind(this)} className="fa fa-search"/></a>
             {this.state.modal_search_clear?<a href="#!"><input type="submit" className="btn btn-info btn-sm" value="Clear" style={{ color:"white",background:"#337ab7",fontSize:"small",marginLeft:"2vw"}} onClick={this.clear_search.bind(this)}></input></a>:null} 
  
  </div>
  <div className="row" style={{display:"flex",flexDirection:"row",margin:"0",width:"100%",height:"40vh"}}>
  <table class="table table-fixed" >
                        <thead className="form_font">
                            
                              <tr>
                                <th scope="col" class="col-3">Room No.</th>
                                <th scope="col" class="col-3">Slot A</th>
                                <th scope="col" class="col-3">Slot B</th>
                                <th scope="col" class="col-3">Slot C</th>
                                
                            </tr>
                        </thead>
                          {this.state.flipside_content!="swap_room"?<tbody style={{height:"30vh"}}>
                       {this.state.select_room_data.map(dt=>{
                     i=i+1;
                       return(
               <tr  key={i} style={{width:"100%"}}>  
               <td scope="row" class="col-3" style={{width:"25%",height:"8vh"}}>{dt.Room_number}</td>
               <td class="col-3" style={{width:"25%",height:"8vh"}}>{dt.Roommates[0]!="Nill"?dt.Roommates[0]:<a href="#!"><input type="submit" className="btn btn-info btn-sm" value="Select" style={{ color:"white",background:"#337ab7",fontSize:"small"}} name="slot A" onClick={this.room_select.bind(this,i-1)}></input></a>}</td>
                <td class="col-3" style={{width:"25%",height:"8vh"}}>{dt.Roommates[1]!="Nill"?dt.Roommates[1]:<a href="#!"><input type="submit" className="btn btn-info btn-sm" value="Select" style={{ color:"white",background:"#337ab7",fontSize:"small"}} name="slot B" onClick={this.room_select.bind(this,i-1)}></input></a>} </td>
                <td class="col-3" style={{width:"25%",height:"8vh"}}>{dt.Roommates[2]!="Nill"?dt.Roommates[2]:<a href="#!"><input type="submit" className="btn btn-info btn-sm" value="Select" style={{ color:"white",background:"#337ab7",fontSize:"small"}} name="slot C" onClick={this.room_select.bind(this,i-1)}></input></a>} </td>  </tr> 
                  
                      )
                   })}
                          
                       </tbody>:<tbody style={{height:"30vh"}}>
                       {this.state.select_room_data.map(dt=>{
                     i=i+1;
                       return(
               <tr  key={i} style={{width:"100%"}}>  
               <td scope="row" class="col-3" style={{width:"25%",height:"8vh"}}>{dt.Room_number}</td>
               <td class="col-3" style={{width:"25%",height:"8vh"}}><a href="#!"><input type="submit" className="btn btn-info btn-sm" value={dt.Roommates[0]!="Nill"?"Swap":"Select"} style={{ color:"white",background:"#337ab7",fontSize:"small",marginLeft:"1vw",border:dt.Roommates[0]!="Nill"?"3px solid red":"none"}} name="slot A" onClick={this.room_select.bind(this,i-1)}></input></a>{dt.Roommates[0]}</td>
                <td class="col-3" style={{width:"25%",height:"8vh"}}><a href="#!"><input type="submit" className="btn btn-info btn-sm" value={dt.Roommates[1]!="Nill"?"Swap":"Select"} style={{ color:"white",background:"#337ab7",fontSize:"small",marginLeft:"1vw",border:dt.Roommates[1]!="Nill"?"3px solid red":"none"}} name="slot B" onClick={this.room_select.bind(this,i-1)}></input></a> {dt.Roommates[1]}</td>
                <td class="col-3" style={{width:"25%",height:"8vh"}}><a href="#!"><input type="submit" className="btn btn-info btn-sm" value={dt.Roommates[2]!="Nill"?"Swap":"Select"} style={{ color:"white",background:"#337ab7",fontSize:"small",marginLeft:"1vw",border:dt.Roommates[2]!="Nill"?"3px solid red":"none"}} name="slot C" onClick={this.room_select.bind(this,i-1)}></input></a> {dt.Roommates[2]}</td>  </tr> 
                  
                      )
                   })}
                          
                       </tbody>}    
                        
                        
                    </table>
  </div>
  <div style={{display:"flex",flexDirection:"row",width:"100%"}}>
    <div style={{width:"68%"}}></div>
  <a href="#!" class="btn btn-primary active" role="button" aria-pressed="true"  className="modal_button_font3"  onClick={this.bt.bind(this,2)} >Go Back</a>
  <a href="#!" class="btn btn-primary active" role="button" aria-pressed="true"  className="modal_button_font3"  onClick={(this.state.selected_room!="Select any room" && this.state.selected_room_slot!="Select Room Slot")?this.confirm.bind(this,1):this.show_msg.bind(this)} >Next</a>
  </div>
  </div>
  </div>
</div>:<div>
<div className="modal-content2 modal_extra2" >
          <div className="row" style={{width:"100%"}}>
  <div className="row" style={{margin:"0 5%",height:"5vh",width:"80%",justifyContent:"center"}} >
   <h5 style={{fontWeight:"bold",marginTop:"1vh"}} >Vacate Room Confirmation</h5>
  </div>
  <div style={{width:"5%"}}>
  <a href="#!" style={{textDecoration:"none"}}><span className="close" id='cls' onClick={this.bt.bind(this,0)} >&times;</span></a>  
  </div>
  <div className="row" style={{width:"100%",margin:"0"}}>
  <div className="row" style={{display:"flex",flexDirection:"row",margin:"3vh 10%",width:"100%"}}>
  <div className="col" style={{width:"100%"}}>
    <div style={{display:"flex",flexDirection:"row"}}><p  style={{color:"black",marginBottom:"0",marginRight:"4vw"}}>Name :</p> <p>{this.state.target_data["name"]}</p></div>
    <div style={{display:"flex",flexDirection:"row"}}><p  style={{color:"black",marginBottom:"0",marginRight:"4vw"}}>Room Number :</p> <p>{this.state.selected_room}</p></div>
    <div style={{display:"flex",flexDirection:"row"}}><p  style={{color:"black",marginBottom:"0",marginRight:"4vw"}}>Slot Number : </p><p> {this.state.selected_room_slot}</p></div>
    <div style={{display:"flex",flexDirection:"row"}}><p  style={{color:"black",marginBottom:"0",marginRight:"4vw"}}>Exit Date : </p><p> {this.state.present_date}</p>
    
    
    </div>
    </div>
    
   </div>
   <div style={{marginTop:"4vh",display:"flex",flexDirection:"row",width:"100%"}}>
     <div style={{width:"60%",height:"100%"}}></div>
  <a href="#!" class="btn btn-primary active" role="button" aria-pressed="true"  className="modal_button_font3"  onClick={this.bt.bind(this,1)} >Confirm</a>
  <a href="#!" class="btn btn-primary active" role="button" aria-pressed="true"  className="modal_button_font3"  onClick={this.bt.bind(this,1)} >Decline</a>
  </div></div>
  </div>
</div></div>}

</div>
      </ReactCardFlip>
:this.state.modal_box===1?<div>
          
  {(this.state.flipside_content!="swap_room" || this.state.swap_person==="Nill")?<div>
  <div className="modal-content2 modal_extra2" >
          <div className="row" style={{width:"100%"}}>
  <div className="row" style={{margin:"0 5%",height:"5vh",width:"80%",justifyContent:"center"}} >
   <h5 style={{fontWeight:"bold",marginTop:"1vh"}} >Room Confirmation</h5>
  </div>
  <div style={{width:"5%"}}>
  <a href="#!" style={{textDecoration:"none"}}><span className="close" id='cls' onClick={this.bt.bind(this,0)} >&times;</span></a>  
  </div>
  
  <div className="row" style={{width:"100%",margin:"0"}}>
  <div className="row" style={{display:"flex",flexDirection:"row",margin:"3vh 10%",width:"100%"}}>
  <div className="col" style={{width:"100%"}}>
    <div style={{display:"flex",flexDirection:"row"}}><p  style={{color:"black",marginBottom:"0",marginRight:"4vw"}}>Name :</p> <p>{this.state.target_data["name"]}</p></div>
    <div style={{display:"flex",flexDirection:"row"}}><p  style={{color:"black",marginBottom:"0",marginRight:"4vw"}}>Room Number :</p> <p>{this.state.selected_room}</p></div>
    <div style={{display:"flex",flexDirection:"row"}}><p  style={{color:"black",marginBottom:"0",marginRight:"4vw"}}>Slot Number : </p><p> {this.state.selected_room_slot}</p></div>
    <div style={{display:"flex",flexDirection:"row"}}><p  style={{color:"black",marginBottom:"0",marginRight:"4vw"}}>{this.state.flipside_content==="swap_room"?"Updation Date":"Entry Date"} : </p><p> {this.state.present_date}</p>
    
    </div>
    </div>
    
   </div>
   <div style={{marginTop:"4vh",display:"flex",flexDirection:"row",width:"100%"}}>
     <div style={{width:"60%",height:"100%"}}></div>
  <a href="#!" class="btn btn-primary active" role="button" aria-pressed="true"  className="modal_button_font3"  onClick={this.bt.bind(this,1)} >Confirm</a>
  <a href="#!" class="btn btn-primary active" role="button" aria-pressed="true"  className="modal_button_font3"  onClick={this.bt.bind(this,1)} >Decline</a>
  </div></div>
  </div></div>
</div>:<div>
    <div className="swap_confirmation">
    <div className="modal-content2 modal_extra3">
          <div className="row" style={{width:"100%"}}>
  <div className="row" style={{margin:"0 5%",height:"5vh",width:"80%",justifyContent:"center"}} >
   <h5 style={{fontWeight:"bold",marginTop:"1vh"}} >Room Confirmation</h5>
  </div>
  <div style={{width:"5%"}}>
  <a href="#!" style={{textDecoration:"none"}}><span className="close" id='cls' onClick={this.bt.bind(this,0)} >&times;</span></a>  
  </div>
    <div className="row" style={{margin:"3% 10%",marginBottom:"0"}}>
                <div className="col category_column col_sp">
                <div className="row" style={{display:"flex",flexDirection:"row",margin:"3vh 10%",width:"100%"}}>
  <div className="col" style={{width:"100%"}}>
    <div style={{display:"flex",flexDirection:"row"}}><p  style={{color:"black",marginBottom:"0",marginRight:"1vw"}}>Name :</p> <p>{this.state.target_data["name"]}</p></div>
    <div style={{display:"flex",flexDirection:"row"}}><p  style={{color:"black",marginBottom:"0",marginRight:"1vw"}}>Room Number :</p> <p>{this.state.selected_room}</p></div>
    <div style={{display:"flex",flexDirection:"row"}}><p  style={{color:"black",marginBottom:"0",marginRight:"1vw"}}>Slot Number : </p><p> {this.state.selected_room_slot}</p></div>
    <div style={{display:"flex",flexDirection:"row"}}><p  style={{color:"black",marginBottom:"0",marginRight:"1vw"}}>Updation Date : </p><p> {this.state.present_date}</p>
    
    </div>
    </div>
    
   </div>
                  </div>
                  <div className="col category_column col_sp">
                  <div className="row" style={{display:"flex",flexDirection:"row",margin:"3vh 10%",width:"100%"}}>
  <div className="col" style={{width:"100%"}}>
    <div style={{display:"flex",flexDirection:"row"}}><p  style={{color:"black",marginBottom:"0",marginRight:"1vw"}}>Name :</p> <p>{this.state.swap_person}</p></div>
    <div style={{display:"flex",flexDirection:"row"}}><p  style={{color:"black",marginBottom:"0",marginRight:"1vw"}}>Room Number :</p> <p>{this.state.target_data["room_no"]}</p></div>
    <div style={{display:"flex",flexDirection:"row"}}><p  style={{color:"black",marginBottom:"0",marginRight:"1vw"}}>Slot Number : </p><p> {this.state.target_data["slot"]}</p></div>
    <div style={{display:"flex",flexDirection:"row"}}><p  style={{color:"black",marginBottom:"0",marginRight:"1vw"}}>Updation Date : </p><p> {this.state.present_date}</p>
    
    </div>
    </div>
    
   </div>
   
                  </div>
                  </div>
                  <div style={{marginTop:"4vh",display:"flex",flexDirection:"row",width:"100%"}}>
     <div style={{width:"60%",height:"100%"}}></div>
  <a href="#!" class="btn btn-primary active" role="button" aria-pressed="true"  className="modal_button_font3"  onClick={this.bt.bind(this,1)} >Confirm</a>
  <a href="#!" class="btn btn-primary active" role="button" aria-pressed="true"  className="modal_button_font3"  onClick={this.bt.bind(this,1)} >Decline</a>
  </div>
    </div>
    </div></div>
    </div>}
  
  </div>:null}
</div>
        <div className="sh" style={{width:"350px",height:"250px",marginBottom:"15vh"}}>
        {this.state.show &&<QrReader
          delay={300}
          onError={this.handleError}
          onScan={this.handleScan}
        />}</div>
      </div>
      
    )
  }
}

export default Qrread