
import React,{Fragment,Component} from 'react';
import './style_authentication.scss'
import PropTypes from 'prop-types'
import { Handles, Rail, Slider, Ticks, Tracks } from 'react-compound-slider';
const railOuterStyle = {
  position: 'absolute',
  width: '100%',
  height: 42,
  transform: 'translate(0%, -50%)',
  borderRadius: 7,
  cursor: 'pointer',
  // border: '1px solid white',
}

const railInnerStyle = {
  position: 'absolute',
  width: '100%',
  height: 14,
  transform: 'translate(0%, -50%)',
  borderRadius: 7,
  pointerEvents: 'none',
  backgroundColor: 'white',
  boxShadow: '0 0 10px  rgba(0,0,0,0.6)'
}

const sliderStyle = {
  position: 'relative',
  width: '100%',
}

class Rooms extends Component{
    constructor(props){
      super(props)
      this.state ={
        slider_domain:[],
        defaultValues:[],
        values: [],
        update: [],
        rooms:[]
      }
      
    }
    change(msg){
      this.setState({
        show_page:msg
      })
    }
    onUpdate = update => {function getZero(n){
      let num=n+""
      if(num.length===1){
        return "00"+n
      }else if(num.length===2){
        return "0"+n
      }else{
        return n+""
      }
    }
    let ab=["001-"+getZero(update[0]),getZero(update[0]+1)+"-"+getZero(update[1]),getZero(update[1]+1)+"-"+getZero(update[2]),getZero(update[2]+1)+"-"+getZero(update[3]),getZero(update[3]+1)+"-"+getZero(update[4]),getZero(update[4]+1)+"-"+getZero(update[5]),getZero(update[5]+1)+"-"+getZero(update[6]),getZero(update[6]+1)+"-"+getZero(update[7]),getZero(update[7]+1)+"-"+getZero(update[8])]
  
      this.setState({ update:update,
                      rooms:ab})      
  this.props.room_update(ab,update);
      }
  
    onChange = values => {
      this.setState({ values })
      
    }
go(){
}

    componentDidMount(){
     this.setState({
        slider_domain:[0,this.props.total_room],
        defaultValues:this.props.dt,
        values: this.props.dt.slice(),
        update: this.props.dt.slice(),
        rooms:this.props.rooms
        
      })

    }
render(){
  
  const {
    state: { values, update },
  } = this
    return(
        <div key={this.props.total_room} style={{paddingBottom:"8vh",paddingTop:"5vh"}}>
            <label for="" ><b>Select the Range for Categories: </b></label>
        <div class="table-responsive text-nowrap" style={{minWidth:"60vw"}}>
        {/* <!--Table--> */}
        <table class="table table-striped " >

          {/* <!--Table head--> */}
          <thead>
            <tr>
            <th class="col-3" style={{fontSize:"0.8rem"}}>1st Year B.tech(Students)</th>
      <th class="col-3" style={{fontSize:"0.8rem"}}>2nd Year B.tech(Students)</th>
      <th class="col-3" style={{fontSize:"0.8rem"}}>3rd Year B.tech(Students)</th>
      <th class="col-3" style={{fontSize:"0.8rem"}}>4th Year B.tech(Students)</th>
            </tr>
          </thead>
          {/* <!--Table head--> */}

          {/* <!--Table body--> */}
          <tbody>
            <tr>
            <td class="col-3" style={{fontSize:"0.8rem"}}>{this.state.rooms[0]}</td>
            <td class="col-3" style={{fontSize:"0.8rem"}}>{this.state.rooms[1]}</td>
            <td class="col-3" style={{fontSize:"0.8rem"}}>{this.state.rooms[2]}</td>
            <td class="col-3" style={{fontSize:"0.8rem"}}>{this.state.rooms[3]}</td>
            </tr>
          
          </tbody>
          {/* <!--Table body--> */}


        </table>
        {/* <!--Table--> */}
         {/* <!--Table--> */}
         <table class="table table-striped" >

{/* <!--Table head--> */}
<thead>
  <tr>
<th class="col-3" style={{fontSize:"0.8rem"}}>1st Year M.tech(Students)</th>
<th class="col-3" style={{fontSize:"0.8rem"}}>2nd Year M.tech(Students)</th>
<th class="col-2" style={{fontSize:"0.8rem"}}>Other</th>
<th class="col-2" style={{fontSize:"0.8rem"}}>Tutors</th>
<th class="col-2" style={{fontSize:"0.8rem"}}>Staff</th>
  </tr>
</thead>
{/* <!--Table head--> */}

{/* <!--Table body--> */}
<tbody>
  <tr>
            <td class="col-3" style={{fontSize:"0.8rem"}}>{this.state.rooms[4]}</td>
            <td class="col-3" style={{fontSize:"0.8rem"}}>{this.state.rooms[5]}</td>
            <td class="col-2" style={{fontSize:"0.8rem"}}>{this.state.rooms[6]}</td>
            <td class="col-2" style={{fontSize:"0.8rem"}}>{this.state.rooms[7]}</td>
            <td class="col-2" style={{fontSize:"0.8rem"}}>{this.state.rooms[8]}</td>
  </tr>

</tbody>
{/* <!--Table body--> */}


</table>
{/* <!--Table--> */}
      </div>
    
    <div style={{ height: "auto", minWidth: '60vw' ,marginTop:"20px"}}>
        <Slider
          mode={3}
          step={1}
          domain={[0,this.props.total_room]}
          rootStyle={sliderStyle}
          onUpdate={this.onUpdate}
          onChange={this.onChange}
          values={values}
        >
          <Rail>
            {({ getRailProps }) => <SliderRail getRailProps={getRailProps} />}
          </Rail>
          <Handles >
            {({ handles, getHandleProps }) => (
              <div className="slider-handles">
                {handles.map(handle => (
                  <KeyboardHandle
                    key={handle.id}
                    handle={handle}
                    domain={this.state.slider_domain}
                    getHandleProps={getHandleProps}
                  />
                ))}
              </div>
            )}
          </Handles>
          <Tracks left={false} right={false}>
            {({ tracks, getTrackProps }) => (
              <div className="slider-tracks">
                {tracks.map(({ id, source, target }) => (
                  <Track
                    key={id}
                    source={source}
                    target={target}
                    getTrackProps={getTrackProps}
                  />
                ))}
              </div>
            )}
          </Tracks>
          <Ticks count={10}>
            {({ ticks }) => (
              <div className="slider-ticks">
                {ticks.map(tick => (
                  <Tick key={tick.id} tick={tick} count={ticks.length} />
                ))}
              </div>
            )}
          </Ticks>
        </Slider>
      </div>
    
        </div>
    )
    
  }
}


// Change the range of Values-code
export function ValueViewer() {
  return (
    <div>
      {this.values}
    </div>
  )
} 

export function SliderRail({ getRailProps }) {
  return (
    <Fragment>
      <div style={railOuterStyle} {...getRailProps()} />
      <div style={railInnerStyle} />
    </Fragment>
  )
}

SliderRail.propTypes = {
  getRailProps: PropTypes.func.isRequired,
}

// *******************************************************
// HANDLE COMPONENT
// *******************************************************
export function Handle({
  domain: [min, max],
  handle: { id, value, percent },
  disabled,
  getHandleProps,
}) {
  return (
    <Fragment>
      <div
        style={{
          left: `${percent}%`,
          position: 'absolute',
          transform: 'translate(-50%, -50%)',
          WebkitTapHighlightColor: 'rgba(0,0,0,0)',
          zIndex: 5,
          width: 28,
          height: 42,
          cursor: 'pointer',
          // border: '1px solid white',
          backgroundColor: 'none',
        }}
        {...getHandleProps(id)}
      />
      <div
        role="slider"
        aria-valuemin={min}
        aria-valuemax={max}
        aria-valuenow={value}
        style={{
          left: `${percent}%`,
          position: 'absolute',
          transform: 'translate(-50%, -50%)',
          zIndex: 2,
          width: 24,
          height: 24,
          borderRadius: '50%',
          boxShadow: '1px 1px 1px 1px rgba(0, 0, 0, 0.3)',
          backgroundColor: disabled ? '#666' : 'white',
        }}
      />
    </Fragment>
  )
}

Handle.propTypes = {
  domain: PropTypes.array.isRequired,
  handle: PropTypes.shape({
    id: PropTypes.string.isRequired,
    value: PropTypes.number.isRequired,
    percent: PropTypes.number.isRequired,
  }).isRequired,
  getHandleProps: PropTypes.func.isRequired,
  disabled: PropTypes.bool,
}

Handle.defaultProps = {
  disabled: false,
}

// *******************************************************
// KEYBOARD HANDLE COMPONENT
// Uses a button to allow keyboard events
// *******************************************************
export function KeyboardHandle({
  domain: [min, max],
  handle: { id, value, percent },
  disabled,
  getHandleProps,
}) {
  return (
    <button
      className="keyboard_style"
      role="slider"
      aria-valuemin={min}
      aria-valuemax={max}
      aria-valuenow={value}
      style={{
        left: `${percent}%`,
        position: 'absolute',
        transform: 'translate(-50%, -50%)',
        zIndex: 2,
        width: 24,
        height: 24,
        borderRadius: '50%',
        boxShadow: 'inset  10px -10px 25px rgba(0,0,0,0.6)',
        backgroundColor :'white',
      }}
      {...getHandleProps(id)}
    />
  )
}

KeyboardHandle.propTypes = {
  domain: PropTypes.array.isRequired,
  handle: PropTypes.shape({
    id: PropTypes.string.isRequired,
    value: PropTypes.number.isRequired,
    percent: PropTypes.number.isRequired,
  }).isRequired,
  getHandleProps: PropTypes.func.isRequired,
  disabled: PropTypes.bool,
}

KeyboardHandle.defaultProps = {
  disabled: false,
}

// *******************************************************
// TRACK COMPONENT
// *******************************************************
export function Track({ source, target, getTrackProps, disabled }) {
  return (
    <div
      style={{
        position: 'absolute',
        transform: 'translate(0%, -50%)',
        height: 14,
        zIndex: 1,
        backgroundColor: disabled ? 'white' : 'white',
        borderRadius: 7,
        cursor: 'pointer',
        left: `${source.percent}%`,
        width: `${target.percent - source.percent}%`,
      }}
      {...getTrackProps()}
    />
  )
}

Track.propTypes = {
  source: PropTypes.shape({
    id: PropTypes.string.isRequired,
    value: PropTypes.number.isRequired,
    percent: PropTypes.number.isRequired,
  }).isRequired,
  target: PropTypes.shape({
    id: PropTypes.string.isRequired,
    value: PropTypes.number.isRequired,
    percent: PropTypes.number.isRequired,
  }).isRequired,
  getTrackProps: PropTypes.func.isRequired,
  disabled: PropTypes.bool,
}

Track.defaultProps = {
  disabled: false,
}

// *******************************************************
// TICK COMPONENT
// *******************************************************
export function Tick({ tick, count, format }) {
  return (
    <div>
      <div
        style={{
          position: 'absolute',
          marginTop: 14,
          width: 1,
          height: 5,
          backgroundColor: 'rgb(200,200,200)',
          left: `${tick.percent}%`,
        }}
      />
      <div
        style={{
          position: 'absolute',
          marginTop: 22,
          fontSize: 10,
          textAlign: 'center',
          marginLeft: `${-(100 / count) / 2}%`,
          width: `${100 / count}%`,
          left: `${tick.percent}%`,
        }}
      >
        {format(tick.value)}
      </div>
    </div>
  )
}

Tick.propTypes = {
  tick: PropTypes.shape({
    id: PropTypes.string.isRequired,
    value: PropTypes.number.isRequired,
    percent: PropTypes.number.isRequired,
  }).isRequired,
  count: PropTypes.number.isRequired,
  format: PropTypes.func.isRequired,
}

Tick.defaultProps = {
  format: d => d,
}
export default Rooms