import React from 'react';
import {Line} from 'react-chartjs-2';
import graph_data from './graph.json'


export default class Graph extends React.Component {
    constructor(){
        super()
        this.state={
            state : {
                labels: [],
                datasets: [
                 
                ]
              },
            month:""
        }
    }
    componentDidMount(){
        this.setState({
            state : {
                labels: [...graph_data.labels],
                datasets: [
                  {
                    label: 'Students Attendence',
                    fill: false,
                    lineTension: 0.5,
                    backgroundColor: 'rgba(75,192,192,1)',
                    borderColor: 'rgba(0,0,0,1)',
                    borderWidth: 2,
                    data: [...graph_data.stu_data]
                  },{
                    label: 'Tutors Attendence',
                    fill: false,
                    lineTension: 0.5,
                    backgroundColor: '#1048A0',
                    borderColor: 'rgba(0,0,0,1)',
                    borderWidth: 2,
                    data: [...graph_data.tu_data]
                  },{
                    label: 'Staff Attendence',
                    fill: false,
                    lineTension: 0.5,
                    backgroundColor: 'green',
                    borderColor: 'rgba(0,0,0,1)',
                    borderWidth: 2,
                    data: [...graph_data.staff_data]
                  }
                ]
              },
            month:graph_data.Month
        })
    }
    componentDidUpdate(){
        
    }
  render() {
      if(this.state.state.labels===[]){
          return(
              <div>Accessing Data....</div>
          )
      }else{
          return (
            <div>
              <Line
                data={this.state.state}
                options={{
                  legend:{
                    display:true,
                    position:'right'
                  }
                }}
              />
            </div>
          );

      }
  }
}