
import { faEye, faPlus, faSearch } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React,{Fragment,Component} from 'react';
import './style_authentication.scss'
import Entry_data from "./Entry_exit_data.json"
import ReactCardFlip from 'react-card-flip';
import Room_data_select from "./Select_room_data.json"
import Qrread from './Qrreader';
class Entry_exit_room extends Component{
    constructor(props){
      super(props)
      this.state ={
        gatepass_show_page:"main",
        mem:"B.tech Student",
        entry_exit_data:[{"name":"no_data","email":"no_data","mobile_number":["null","null"],"status":"no_data"}],
        num:0,
        modal_box:0,
        isFlipped: false,
        select_room_data:[],
        modal_search_clear:false,
        selected_room:"Select any room",
        selected_room_slot:"Select Room Slot",
        selected_individual_data:{},
        present_date:"",
        flipside_conten:"room_selection",
        swap_person:"",
        show:false,
        entry_exit_search_load:true,
        entry_exit_search_data:{},
        entry_exit_user_table_handler:"0"
      }
      
    this.handleClick = this.handleClick.bind(this);
    }
    change_member_selection(event){
      this.setState({mem:event.target.value})
    }
    componentDidMount(){
      var today = new Date();
      var dd = String(today.getDate()).padStart(2, '0');
      var mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!
      var yyyy = today.getFullYear();
      
      today = mm + '/' + dd + '/' + yyyy;
      this.setState({
        present_date:today,
        entry_exit_search_load:true,
      })
    }
    show_modal_box(i){
        this.setState({
            num:i,
            select_room_data:Room_data_select["data"],
            selected_individual_data:Room_data_select
        })
        if( document.getElementById("myModal5").style.display = "none"){
            document.getElementById("myModal5").style.display = "block";
          } 
    }
    change(msg){
      this.setState({
        gatepass_show_page:msg
    })
    }
    bt(i){
      if(i===2){
        this.setState({
          isFlipped:false
        })
      }else{
        if( document.getElementById("myModal5").style.display = "block"){
            document.getElementById("myModal5").style.display = "none";
          } 
          this.setState({
            modal_box:0,
            isFlipped:false
          })
      }
    }
    confirm(msg){
      if(msg===1){
        this.setState({
          modal_box:msg
        })
      }else{
        if( document.getElementById("myModal5").style.display = "block"){
            document.getElementById("myModal5").style.display = "none";
          } 
      }
    }
    access_data_function(){
      // modify this function with the data fetching statement in backend work
        
      this.setState({entry_exit_search_load:false})
      setTimeout(()=>{
        let j=0,k=0,chk={},arr=[];
      for(let i=0;i<Entry_data["data"].length;i++){
        if(j>9){
          chk[k]=arr;
            arr=[];
            j=-1;
            k=k+1;
        }
          arr.push(Entry_data["data"][i]);
          j++;
          if(i===Entry_data["data"].length-1){
            chk[k]=arr;
          }
      }
        this.setState({entry_exit_data:chk[this.state.entry_exit_user_table_handler],entry_exit_search_load:true,entry_exit_search_data:chk})
      },2000)
    }
    clear_data_function(){
      this.setState({
        entry_exit_data:[{"name":"no_data","email":"no_data","mobile_number":["null","null"],"status":"no_data"}],
        entry_exit_search_data:{},
        entry_exit_user_table_handler:"0"
      })
    }
    entry_exit_search_previous(){
      this.setState({entry_exit_user_table_handler:(parseInt(this.state.entry_exit_user_table_handler)-1)+"",entry_exit_data:this.state.entry_exit_search_data[(parseInt(this.state.entry_exit_user_table_handler)-1)+""]})
    }
    entry_exit_search_next(){
      this.setState({entry_exit_user_table_handler:(parseInt(this.state.entry_exit_user_table_handler)+1)+"",entry_exit_data:this.state.entry_exit_search_data[(parseInt(this.state.entry_exit_user_table_handler)+1)+""]})
    }
    handleClick(msg) {
      this.setState(prevState => ({ isFlipped: !prevState.isFlipped }));
      this.setState({
        flipside_content:msg
      })
    }
    
    find(){
      // this setState to update select_room_data must be updated in backend 
      let ab = document.getElementById("inptxt").value,no=0;
      if(parseInt(ab)){
        let arr= this.state.select_room_data,array =[];
        arr.map(dt=>{
          if(parseInt(dt["Room_number"])===parseInt(ab)){
           array.push(dt)
           this.setState({
            select_room_data:array
          })
            no=1;
          }
        })
  
        if(no===0){
          this.setState({
          select_room_data:[{"Room_number":"no_data","Roommates":["no_data","no_data","no_data"]}]
        })
        }
        this.setState({
          modal_search_clear:true
        })

      }
    }
    clear_search(){
      this.setState({
        select_room_data:Room_data_select["data"],
        modal_search_clear:false,
        selected_room:"Select any room",
        selected_room_slot:"Select Room Slot"
      })
    }
    room_select(msg,event){
      let arr= this.state.select_room_data,num=0
      if(event.target.name==="slot B"){
        num=1;
      }else if(event.target.name==="slot C"){
        num=2;
      }
      this.setState({
        selected_room:arr[msg]["Room_number"],
        selected_room_slot:event.target.name,
        swap_person:arr[msg]["Roommates"][num]
      })
    }
    show_msg(){
      alert("Please Select a Room.");
    }
    qr(){
      this.setState({
        show:!this.state.show
      })
    }
    qr_close(){
      this.setState({
        show:false
      })
    }
render(){
    let i=0,j=0;
    return(
        <div >
            <div id="myModal5" className="modal">
              {this.state.modal_box===0?
<ReactCardFlip isFlipped={this.state.isFlipped} flipDirection="vertical">
<div>
  <div className="modal-content" id="modal_con2">
  <div className="row" style={{margin:"0"}}>
  <div className="col modal_col" >
      <img src={this.state.entry_exit_data[this.state.num]?this.state.entry_exit_data[this.state.num]["img"]:null} alt='the profile picture' className="img_modal"/> 
  </div>
  <div className="col modal_col modal_col_extra" style={{height:"auto"}}>
                  <a href="#!" style={{textDecoration:"none",}}><span className="close" id='cls' onClick={this.bt.bind(this,0)} style={{color:"black"}}>&times;</span></a>  
      <div  style={{fontFamily:"Roboto, sans-serif"}} className="sp2">
        <center><h2 style={{fontWeight:"bold"}} className="modal_head_fontcap">{this.state.entry_exit_data[this.state.num]?this.state.entry_exit_data[this.state.num]["name"]:null}</h2>
        <p  className="modal_head_font">Email: {this.state.entry_exit_data[this.state.num]?this.state.entry_exit_data[this.state.num]["email"]:null}</p></center>
      </div>
      <div  style={{fontFamily:"Roboto, sans-serif"}} className="sp2">
      <center>
        <p  className="modal_head_font2" style={{color:"black",marginBottom:"0"}}>Status: {this.state.entry_exit_data[this.state.num]?this.state.entry_exit_data[this.state.num]["status"]:null}
      </p>
              {this.state.entry_exit_data[this.state.num]["status"]==="Occupied"?<p  className="modal_head_font2" style={{color:"black",marginBottom:"0"}}>Room Number: {this.state.entry_exit_data[this.state.num]?this.state.entry_exit_data[this.state.num]["room_no"]:null}</p>:null}
              {this.state.entry_exit_data[this.state.num]["status"]==="Active"?<p  className="modal_head_font2" style={{color:"black",marginBottom:"0"}}>Previous Room Number: {this.state.entry_exit_data[this.state.num]?this.state.entry_exit_data[this.state.num]["Previous_rooms"].map(dt=>{
                return("["+dt+"]")
              }):null}</p>:null}
              <p  className="modal_head_font2" style={{color:"black",marginBottom:"0"}}>Mobile Number 1: {this.state.entry_exit_data[this.state.num]?this.state.entry_exit_data[this.state.num]["mobile_number"][0]:null}</p>
              <p  className="modal_head_font2" style={{color:"black",marginBottom:"0"}}>Mobile Number 2: {this.state.entry_exit_data[this.state.num]?this.state.entry_exit_data[this.state.num]["mobile_number"][1]:null}</p>
              
              {this.state.entry_exit_data[this.state.num]["status"]==="Occupied"?
              <div><div style={{marginTop:"2vh"}}>
              <a href="#!" class="btn btn-primary btn-lg active" role="button" aria-pressed="true"  className="modal_button_font"  onClick={this.handleClick.bind(this,"vacate_room")}>Vacate Room</a>
              </div><div style={{marginTop:"4vh"}}>
              <a href="#!" class="btn btn-primary btn-lg active" role="button" aria-pressed="true"  className="modal_button_font"  onClick={this.handleClick.bind(this,"swap_room")}>Swap Room</a>
              </div></div>:this.state.entry_exit_data[this.state.num]["status"]==="Active"?<div style={{marginTop:"4vh"}}>
              <a href="#!" class="btn btn-primary btn-lg active" role="button" aria-pressed="true"  className="modal_button_font"  onClick={this.handleClick.bind(this,"room_selection")}>Select Room</a>
              </div>:null}
              </center>
      </div>
  </div>
</div>
</div></div>
<div>
  {(this.state.flipside_content==="room_selection" || this.state.flipside_content==="swap_room")?
  <div className="modal-content modal_extra" id="modal_con" >
  <div className="attend_info_row2" style={{margin:"0"}} >
    <div className="row" style={{width:"100%"}}>
  <div className="row" style={{margin:"0 5%",height:"5vh",width:"80%",justifyContent:"center"}} >
    {this.state.flipside_conten==="room_selection"?
   <h5 style={{fontWeight:"bold",marginTop:"1vh"}} >Room Selection</h5>:
   <h5 style={{fontWeight:"bold",marginTop:"1vh"}} >Swap Room Selection</h5>}
  </div>
  <div style={{width:"5%"}}>
  <a href="#!" style={{textDecoration:"none"}}><span className="close" id='cls'style={{color:"black"}} onClick={this.bt.bind(this,0)} >&times;</span></a>  
  </div>
  </div>
  <div className="row" style={{width:"100%",margin:"0"}}>
    {this.state.flipside_content==="swap_room"?<div className="row" style={{display:"flex",flexDirection:"row",margin:"1vh 5%",width:"100%"}}>
    <div className="col" style={{width:"50%",display:"flex",flexDirection:"row"}}>
    <p  style={{color:"black",marginBottom:"0"}}>Current Room Number :</p> <p>{this.state.entry_exit_data[this.state.num]?this.state.entry_exit_data[this.state.num]["room_no"]:null}</p></div>
    
  </div>:null}
  <div className="row" style={{display:"flex",flexDirection:"row",margin:"0.3vh 5%",width:"100%"}}>
    <div className="col" style={{width:"50%",display:"flex",flexDirection:"row"}}>
    <p  style={{color:"black",marginBottom:"0"}}>Room Number :</p> <p>{this.state.selected_room}</p></div>
    <div className="col" style={{width:"50%",display:"flex",flexDirection:"row"}}>
      <p  style={{color:"black",marginBottom:"0"}}>Slot Number : </p><p> {this.state.selected_room_slot}</p>
    </div>
  </div>
  <div className="row" style={{display:"flex",flexDirection:"row",margin:"0.3vh 5%",width:"100%"}}>
    <div className="col" style={{width:"50%",display:"flex",flexDirection:"row"}}>
    <p  style={{color:"black",marginBottom:"0"}}>Batch : {this.state.selected_individual_data["Batch"]}</p></div>
    <div className="col" style={{width:"50%",display:"flex",flexDirection:"row"}}>
      <p  style={{color:"black",marginBottom:"0"}}>Allocated Rooms :{this.state.selected_individual_data["Allocated_Rooms"]?this.state.selected_individual_data["Allocated_Rooms"].map(dt=>{
        return("["+dt+"]")
      }):null}</p>
    </div>
  </div>
  <div className="row" style={{display:"flex",flexDirection:"row",margin:"1vh 0",width:"100%",height:"4vh",borderTop:"1px solid #ECEEF0"}}>
              <div style={{width:"60%"}}></div>
              <div><input type="text" placeholder="Search by Room Number" className="form-control" name="search" id="inptxt" style={{height:"3vh",fontSize:"0.7rem",marginLeft:"2vw",marginTop:"1vh"}}/>
              </div>
              <a href="#!" style={{marginTop:"1vh"}}><FontAwesomeIcon icon={faSearch}  onClick={this.find.bind(this)} className="fa fa-search"/></a>
             {this.state.modal_search_clear?<a href="#!"><input type="submit" className="btn btn-info btn-sm" value="Clear" style={{ color:"white",background:"#337ab7",fontSize:"small",marginLeft:"2vw"}} onClick={this.clear_search.bind(this)}></input></a>:null} 
  
  </div>
  <div className="row" style={{display:"flex",flexDirection:"row",margin:"0",width:"100%",height:"40vh"}}>
  <table class="table table-fixed" >
                        <thead className="form_font">
                            
                              <tr>
                                <th scope="col" class="col-3">Room No.</th>
                                <th scope="col" class="col-3">Slot A</th>
                                <th scope="col" class="col-3">Slot B</th>
                                <th scope="col" class="col-3">Slot C</th>
                                
                            </tr>
                        </thead>
                          {this.state.flipside_content!="swap_room"?<tbody style={{height:"30vh"}}>
                       {this.state.select_room_data.map(dt=>{
                     i=i+1;
                       return(
               <tr  key={i} style={{width:"100%"}}>  
               <td scope="row" class="col-3" style={{width:"25%",height:"8vh"}}>{dt.Room_number}</td>
               <td class="col-3" style={{width:"25%",height:"8vh"}}>{dt.Roommates[0]!="Nill"?dt.Roommates[0]:<a href="#!"><input type="submit" className="btn btn-info btn-sm" value="Select" style={{ color:"white",background:"#337ab7",fontSize:"small"}} name="slot A" onClick={this.room_select.bind(this,i-1)}></input></a>}</td>
                <td class="col-3" style={{width:"25%",height:"8vh"}}>{dt.Roommates[1]!="Nill"?dt.Roommates[1]:<a href="#!"><input type="submit" className="btn btn-info btn-sm" value="Select" style={{ color:"white",background:"#337ab7",fontSize:"small"}} name="slot B" onClick={this.room_select.bind(this,i-1)}></input></a>} </td>
                <td class="col-3" style={{width:"25%",height:"8vh"}}>{dt.Roommates[2]!="Nill"?dt.Roommates[2]:<a href="#!"><input type="submit" className="btn btn-info btn-sm" value="Select" style={{ color:"white",background:"#337ab7",fontSize:"small"}} name="slot C" onClick={this.room_select.bind(this,i-1)}></input></a>} </td>  </tr> 
                  
                      )
                   })}
                          
                       </tbody>:<tbody style={{height:"30vh"}}>
                       {this.state.select_room_data.map(dt=>{
                     i=i+1;
                       return(
               <tr  key={i} style={{width:"100%"}}>  
               <td scope="row" class="col-3" style={{width:"25%",height:"8vh"}}>{dt.Room_number}</td>
               <td class="col-3" style={{width:"25%",height:"8vh"}}><a href="#!"><input type="submit" className="btn btn-info btn-sm" value={dt.Roommates[0]!="Nill"?"Swap":"Select"} style={{ color:"white",background:"#337ab7",fontSize:"small",marginLeft:"1vw",border:dt.Roommates[0]!="Nill"?"3px solid red":"none"}} name="slot A" onClick={this.room_select.bind(this,i-1)}></input></a>{dt.Roommates[0]}</td>
                <td class="col-3" style={{width:"25%",height:"8vh"}}><a href="#!"><input type="submit" className="btn btn-info btn-sm" value={dt.Roommates[1]!="Nill"?"Swap":"Select"} style={{ color:"white",background:"#337ab7",fontSize:"small",marginLeft:"1vw",border:dt.Roommates[1]!="Nill"?"3px solid red":"none"}} name="slot B" onClick={this.room_select.bind(this,i-1)}></input></a> {dt.Roommates[1]}</td>
                <td class="col-3" style={{width:"25%",height:"8vh"}}><a href="#!"><input type="submit" className="btn btn-info btn-sm" value={dt.Roommates[2]!="Nill"?"Swap":"Select"} style={{ color:"white",background:"#337ab7",fontSize:"small",marginLeft:"1vw",border:dt.Roommates[2]!="Nill"?"3px solid red":"none"}} name="slot C" onClick={this.room_select.bind(this,i-1)}></input></a> {dt.Roommates[2]}</td>  </tr> 
                  
                      )
                   })}
                          
                       </tbody>}    
                        
                        
                    </table>
  </div>
  <div style={{display:"flex",flexDirection:"row",width:"100%"}}>
    <div style={{width:"68%"}}></div>
  <a href="#!" class="btn btn-primary active" role="button" aria-pressed="true"  className="modal_button_font3"  onClick={this.bt.bind(this,2)} >Go Back</a>
  <a href="#!" class="btn btn-primary active" role="button" aria-pressed="true"  className="modal_button_font3"  onClick={(this.state.selected_room!="Select any room" && this.state.selected_room_slot!="Select Room Slot")?this.confirm.bind(this,1):this.show_msg.bind(this)} >Next</a>
  </div>
  </div>
  </div>
</div>:<div>
<div className="modal-content2 modal_extra2" style={{minHeight:"10vh"}}>
          <div className="row" style={{width:"100%"}}>
  <div className="row" style={{margin:"0 5%",height:"5vh",width:"80%",justifyContent:"center"}} >
   <h5 style={{fontWeight:"bold",marginTop:"1vh"}} >Vacate Room Confirmation</h5>
  </div>
  <div style={{width:"5%"}}>
  <a href="#!" style={{textDecoration:"none"}}><span className="close" id='cls' onClick={this.bt.bind(this,0)} >&times;</span></a>  
  </div>
  <div className="row" style={{width:"100%",margin:"0",marginTop:"10vh"}}>
  <div className="row" style={{display:"flex",flexDirection:"row",margin:"3vh 20%",width:"100%"}}>
  <div className="col" style={{width:"100%"}}>
    <div style={{display:"flex",flexDirection:"row"}}><p  style={{color:"black",marginBottom:"0",marginRight:"4vw"}}>Name :</p> <p>{this.state.entry_exit_data[this.state.num]?this.state.entry_exit_data[this.state.num]["name"]:null}</p></div>
    <div style={{display:"flex",flexDirection:"row"}}><p  style={{color:"black",marginBottom:"0",marginRight:"4vw"}}>Exit Date : </p><p> {this.state.present_date}</p>
    
    
    </div>
    </div>
    
   </div>
   <div style={{marginTop:"4vh",display:"flex",flexDirection:"row",width:"100%"}}>
     <div style={{width:"15%",height:"100%"}}></div>
  <a href="#!" class="btn btn-primary active" role="button" aria-pressed="true"  className="modal_button_font2"  onClick={this.bt.bind(this,1)} style={{marginRight:"3vw"}}>Confirm</a>
  <a href="#!" class="btn btn-primary active" role="button" aria-pressed="true"  className="modal_button_font2"  onClick={this.bt.bind(this,1)} >Decline</a>
  </div></div>
  </div>
</div></div>}

</div>
      </ReactCardFlip>
:this.state.modal_box===1?<div>
          
  {(this.state.flipside_content!="swap_room" || this.state.swap_person==="Nill")?<div>
  <div className="modal-content2 modal_extra2" >
          <div className="row" style={{width:"100%"}}>
  <div className="row" style={{margin:"0 5%",height:"5vh",width:"80%",justifyContent:"center"}} >
   <h5 style={{fontWeight:"bold",marginTop:"1vh"}} >Room Confirmation</h5>
  </div>
  <div style={{width:"5%"}}>
  <a href="#!" style={{textDecoration:"none"}}><span className="close" id='cls' onClick={this.bt.bind(this,0)} >&times;</span></a>  
  </div>
  
  <div className="row" style={{width:"100%",margin:"0"}}>
  <div className="row" style={{display:"flex",flexDirection:"row",margin:"3vh 20%",width:"100%"}}>
  <div className="col" style={{width:"100%"}}>
    <div style={{display:"flex",flexDirection:"row"}}><p  style={{color:"black",marginBottom:"0",marginRight:"4vw"}}>Name :</p> <p>{this.state.entry_exit_data[this.state.num]?this.state.entry_exit_data[this.state.num]["name"]:null}</p></div>
    <div style={{display:"flex",flexDirection:"row"}}><p  style={{color:"black",marginBottom:"0",marginRight:"4vw"}}>Room Number :</p> <p>{this.state.selected_room}</p></div>
    <div style={{display:"flex",flexDirection:"row"}}><p  style={{color:"black",marginBottom:"0",marginRight:"4vw"}}>Slot Number : </p><p> {this.state.selected_room_slot}</p></div>
    <div style={{display:"flex",flexDirection:"row"}}><p  style={{color:"black",marginBottom:"0",marginRight:"4vw"}}>{this.state.flipside_content==="swap_room"?"Updation Date":"Entry Date"} : </p><p> {this.state.present_date}</p>
    
    </div>
    </div>
    
   </div>
   <div style={{marginTop:"4vh",display:"flex",flexDirection:"row",width:"100%"}}>
     <div style={{width:"15%",height:"100%"}}></div>
  <a href="#!" class="btn btn-primary active" role="button" aria-pressed="true"  className="modal_button_font2"  onClick={this.bt.bind(this,1)} style={{marginRight:"3vw"}}>Confirm</a>
  <a href="#!" class="btn btn-primary active" role="button" aria-pressed="true"  className="modal_button_font2"  onClick={this.bt.bind(this,1)} >Decline</a>
  </div></div>
  </div></div>
</div>:<div>
    <div className="swap_confirmation">
    <div className="modal-content2 modal_extra3">
          <div className="row" style={{width:"100%"}}>
  <div className="row" style={{margin:"0 5%",height:"5vh",width:"80%",justifyContent:"center"}} >
   <h5 style={{fontWeight:"bold",marginTop:"1vh"}} >Room Confirmation</h5>
  </div>
  <div style={{width:"5%"}}>
  <a href="#!" style={{textDecoration:"none"}}><span className="close" id='cls' onClick={this.bt.bind(this,0)} >&times;</span></a>  
  </div>
    <div className="row" style={{margin:"3% 10%",marginBottom:"0"}}>
                <div className="col category_column col_sp">
                <div className="row" style={{display:"flex",flexDirection:"row",margin:"3vh 10%",width:"100%"}}>
  <div className="col" style={{width:"100%"}}>
    <div style={{display:"flex",flexDirection:"row"}}><p  style={{color:"black",marginBottom:"0",marginRight:"1vw"}}>Name :</p> <p>{this.state.entry_exit_data[this.state.num]?this.state.entry_exit_data[this.state.num]["name"]:null}</p></div>
    <div style={{display:"flex",flexDirection:"row"}}><p  style={{color:"black",marginBottom:"0",marginRight:"1vw"}}>Room Number :</p> <p>{this.state.selected_room}</p></div>
    <div style={{display:"flex",flexDirection:"row"}}><p  style={{color:"black",marginBottom:"0",marginRight:"1vw"}}>Slot Number : </p><p> {this.state.selected_room_slot}</p></div>
    <div style={{display:"flex",flexDirection:"row"}}><p  style={{color:"black",marginBottom:"0",marginRight:"1vw"}}>Updation Date : </p><p> {this.state.present_date}</p>
    
    </div>
    </div>
    
   </div>
                  </div>
                  <div className="col category_column col_sp">
                  <div className="row" style={{display:"flex",flexDirection:"row",margin:"3vh 10%",width:"100%"}}>
  <div className="col" style={{width:"100%"}}>
    <div style={{display:"flex",flexDirection:"row"}}><p  style={{color:"black",marginBottom:"0",marginRight:"1vw"}}>Name :</p> <p>{this.state.swap_person}</p></div>
    <div style={{display:"flex",flexDirection:"row"}}><p  style={{color:"black",marginBottom:"0",marginRight:"1vw"}}>Room Number :</p> <p>{this.state.entry_exit_data[this.state.num]?this.state.entry_exit_data[this.state.num]["room_no"]:null}</p></div>
    <div style={{display:"flex",flexDirection:"row"}}><p  style={{color:"black",marginBottom:"0",marginRight:"1vw"}}>Slot Number : </p><p> {this.state.entry_exit_data[this.state.num]?this.state.entry_exit_data[this.state.num]["slot"]:null}</p></div>
    <div style={{display:"flex",flexDirection:"row"}}><p  style={{color:"black",marginBottom:"0",marginRight:"1vw"}}>Updation Date : </p><p> {this.state.present_date}</p>
    
    </div>
    </div>
    
   </div>
   
                  </div>
                  </div>
                  <div style={{marginTop:"4vh",display:"flex",flexDirection:"row",width:"100%"}}>
     <div style={{width:"30%",height:"100%"}}></div>
  <a href="#!" class="btn btn-primary active" role="button" aria-pressed="true"  className="modal_button_font2"  onClick={this.bt.bind(this,1)} style={{marginRight:"3vw"}}>Confirm</a>
  <a href="#!" class="btn btn-primary active" role="button" aria-pressed="true"  className="modal_button_font2"  onClick={this.bt.bind(this,1)} >Decline</a>
  </div>
    </div>
    </div></div>
    </div>}
  
  </div>:null}
</div>
             {/* Search Membar page */}
             {this.state.gatepass_show_page==="main"?<div>
             <div className="he">
          <div className="head">
            <div > <h5 ><b>Entry/Exit Authentication</b></h5></div>
            
          </div>
          <div style={{paddingTop:"1.5vh", marginRight:"1vw"}}> <a href="#"><input type="submit" className="btn btn-info btn-sm" value="Use Qrcode Scannar" style={{ color:"white",background:"#337ab7",fontSize:"small",float:"right",marginLeft:"2vw"}} onClick={this.change.bind(this,"qrcode_scanner")}></input></a></div>
       
        </div>
        <div className="he" style={{border:"0"}}>
          <div className="head">
            <div> <h6><b>Filter Box</b></h6></div>
             
          </div>
          </div>
          <div className="authentication_main_content" style={{marginBottom:"6vh"}}>
          <div className="filter_box">
            <div className="row" style={{margin:"0",paddingTop:"1vh",paddingLeft:"2vw"}}><h6><b>Filter</b></h6></div>
            <form>   
            <div className="row" style={{margin:"0",paddingTop:"1vh",paddingLeft:"5vw"}}>
              <div className="form-group form_width" style={{marginRight:"5%"}}>
    <label for="exampleInputUsernaem"><b>Username: </b></label>
    <input type="text" className="form-control" id="exampleInputUsername"  placeholder="Enter username" required/>
  </div>
  <div className="form-group form_width" style={{marginRight:"5%"}}>
  <form action="" >
            <label for="exampleInputSelection"><b>Current designation: </b></label>
            <select name="mode_selection" id="mode_selection" className="form-control" onChange={this.change_member_selection.bind(this)}>
            <option value="B.tech Student">B.tech Student</option>
            <option value="M.tech Student">M.tech Student</option>
            <option value="Others(Student)">Others(Student)</option>
            <option value="Non-Student">Tutors,Staff</option>
            </select>
            </form>
  </div>
  
  {(this.state.mem==="B.tech Student" || this.state.mem==="M.tech Student" || this.state.mem==="Others(Student)")?<div className="form-group form_width" style={{marginRight:"5%"}}>
    <label for="exampleInputBatch"><b>Batch(optional): </b></label>
    <input type="text" class="form-control" id="exampleInputBatch"  placeholder="2018-2022" />
  </div>:<div className="form-group form_width" style={{marginRight:"5%"}}>
    <label for="exampleInputBatch"><b>Year of Join(optional): </b></label>
    <input type="text" class="form-control" id="exampleInputBatch"  placeholder="2018" />
  </div>}
  
  
            </div>
            <div className="row" style={{margin:"0",paddingTop:"1vh",paddingLeft:"5vw"}}>
    
  <button type="submit" class="btn btn-primary" style={{backgroundColor:"#337ab7",marginRight:"3vw",marginTop:"2vh"}} onClick={this.access_data_function.bind(this)}>Search</button>
  <button type="submit" class="btn btn-primary" style={{backgroundColor:"#337ab7",marginRight:"3vw",marginTop:"2vh"}} onClick={this.clear_data_function.bind(this)}>Clear Search</button>
  </div>
              </form>
          </div>
          <div className="row" style={{margin:"0",overflowY:"hidden",overflowX:"scroll",width:"100%",height:"55h",marginBottom:"5vh",marginTop:"6vh"}}>
<div className="he" style={{border:"0",marginBottom:"3vh"}}>
          <div className="head">
            <div> <h6><b>Search Results</b></h6></div>
             
          </div>
          </div>
<table class="table table-fixed">
                        <thead className="thead-dark form_font">
                            
                              <tr>
                              <th scope="col" class="col-1">#</th>
                                <th scope="col" class="col-3">Name</th>
                                <th scope="col" class="col-4">Email</th>
                                <th scope="col" class="col-2">Status</th>
                                <th scope="col" class="col-2">Action</th>
                                
                            </tr>
                        </thead>
                              
                       <tbody>
                         {this.state.entry_exit_search_load?(this.state.entry_exit_data.map(dt=>{
                     j=j+1;
                       return(
               <tr  key={j}>  
               <th scope="row" class="col-1">{j}</th>
               <td class="col-3">{dt.name}</td>
                <td class="col-4">{dt.email} </td>
                <td class="col-2">{dt.status} </td>
                <td class="col-2"> <a href="#!"><FontAwesomeIcon icon={faEye} className="ic fa-2x" aria-hidden="true" onClick={dt.name==="no_data"?null:(dt.status==="Active"?this.show_modal_box.bind(this,j-1,0):this.show_modal_box.bind(this,j-1,1))}/></a></td>
                 </tr> 
                       )
                   })):<div className="preloader2"></div>}
                       
                          
                       </tbody>
                        
                    </table>
                    
                    </div>
                    <div style={{display:"flex",flexDirection:"row"}}>
                      <div style={{width:"80%"}}></div>
                    <div style={{paddingTop:"0.5vh"}}> <a href="#!" onClick={this.entry_exit_search_previous.bind(this)}><input type="submit" className="btn btn-info btn-sm" value="Previous" style={{ color:"white",background:"#337ab7",fontSize:"small",float:"right",marginLeft:"2vw"}} disabled={this.state.entry_exit_user_table_handler==="0"|| !this.state.entry_exit_search_data["0"]}></input></a></div>
                    <div style={{paddingTop:"0.5vh" }}> <a href="#!" onClick={this.entry_exit_search_next.bind(this)}><input type="submit" className="btn btn-info btn-sm" value="Next" style={{ color:"white",background:"#337ab7",fontSize:"small",float:"right",marginLeft:"1vw"}} disabled={this.state.entry_exit_user_table_handler===Object.keys(this.state.entry_exit_search_data).pop()|| !this.state.entry_exit_search_data["0"]}></input></a></div>
                    </div>
          </div>
             </div>:<div> <div className="he">
          <div className="head">
            <div > <h5 ><b>Gate Pass Authentication</b></h5></div>
            
          </div>
          <div style={{paddingTop:"1.5vh", marginRight:"1vw"}}> <a href="#"><input type="submit" className="btn btn-info btn-sm" value="Go Back" style={{ color:"white",background:"#337ab7",fontSize:"small",float:"right",marginLeft:"2vw"}} onClick={this.change.bind(this,"main")}></input></a></div>
       
        </div>
        <div className="he" style={{border:"0"}}>
          <div className="head">
            <div> <h6><b>Qrcode Scanner</b></h6></div>
             
          </div>
          </div>
          <div className="qr" >
          {!this.state.show && <center><img src="data:image/png;base64," alt="qrcode" className="qrc"/>
                <div className="down">
                  <div className="info-q">
                    Scan your QR code to display the gate pass confirmation
                  </div>
                  
                 
                </div>
        </center>}<center>
        {this.state.show && <Qrread call={this.qr_close.bind(this)} />}</center>
        {/* <center>
        {this.state.show && <Barcodereader call={this.qr_close} />}</center> */}
          
          {!this.state.show? <span className="dot" ><FontAwesomeIcon icon={faPlus}   className="fa fa-search qrcd" onClick={this.qr.bind(this)}/></span>:<div style={{paddingTop:"1.5vh", marginRight:"10%"}}> <a href="#"><input type="submit" className="btn btn-info btn-sm" value="Close" style={{ color:"white",background:"#337ab7",fontSize:"small",float:"right",marginLeft:"2vw",marginBottom:"3vh"}} onClick={this.qr_close.bind(this)}></input></a></div>
       }
          
          </div>
          </div>}
         
        </div>
    )
    
  }
}

export default Entry_exit_room