import React from "react";
import './style_input_attendance.scss';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {faArrowDown,faEye,faTrash } from '@fortawesome/free-solid-svg-icons'
import { CenterFocusStrong } from "@material-ui/icons";

class Table extends React.Component{
    constructor(props){
        super()
        this.state={
            tbodyData:props.data,
                currentPage: 1,
                todosPerPage: 5,
                pagesPerScreen:3
        };

       
        this.handleClick = this.handleClick.bind(this);
        this.sort_by_name = this.sort_by_name.bind(this);
        this.sort_by_room = this.sort_by_room.bind(this);    
        this.attendance_btn = this.attendance_btn.bind(this);
        this.handleClickNext = this.handleClickNext.bind(this);
        this.handleClickPrevious = this.handleClickPrevious.bind(this);
        this.show_popup = this.show_popup.bind(this);
        this.bt = this.bt.bind(this);

    }

    show_popup(){
      if( document.getElementById("myModal2").style.display = "none"){
        document.getElementById("myModal2").style.display = "block";}
    }
    bt(){
      if( document.getElementById("myModal2").style.display = "block"){
        document.getElementById("myModal2").style.display = "none";}
    }
    attendance_btn(e){
       console.log(e.target)
    //     const tbodyData=[];
    //     const data=this.state.tbodyData;
    //     data.map((user)=>{
    //         tbodyData.push(user)
    //         if(user.id==e.target.id){
    //            user.attendance=e.target.attendance
    //         }
    //         tbodyData.push(user)

    //     })
    //    console.log(tbodyData)
    //     // this.setState({
    //     //     tbodyData:tbodyData
    //     // })
        
    }
    handleClick(event) {
        this.setState({
          currentPage: Number(event.target.id)
        });
    
        
    

        
      }

      handleClickNext(event){
        var n=Math.ceil(this.state.tbodyData.length/this.state.todosPerPage)%this.state.pagesPerScreen;
        if(this.state.currentPage<=Math.ceil(this.state.tbodyData.length/this.state.todosPerPage)-n){
          this.setState({
            currentPage:this.state.currentPage+this.state.pagesPerScreen
          })
        }
       
    } 

    handleClickPrevious(event){
      if(this.state.currentPage!=1){
        this.setState({
          currentPage:this.state.currentPage-this.state.pagesPerScreen
        })
      }
      
    }

    sort_by_name(){
        const namearr=[];
        this.state.tbodyData.map((data)=>{
            
            namearr.push([data.id,data.items])
            

        })
        namearr.sort(function(a, b) {
            return a[1][0].localeCompare(b[1][0]);
        });
        const tbodyData=[]
      namearr.map((element)=>{
        tbodyData.push({id:element[0],items:element[1]})
      })
        this.setState({
            tbodyData:tbodyData
        })
       

        


    }
     
    sort_by_room(){
        const arr=[];
        this.state.tbodyData.map((data)=>{
            
            arr.push([data.id,data.items,])
            

        })
        arr.sort(function(a, b) {
            return  a[1][1] - b[1][1];
        });
        const tbodyData=[]
      arr.map((element)=>{
        tbodyData.push({id:element[0],items:element[1]})
      })
        this.setState({
            tbodyData:tbodyData
        })
       

        


    }
      

    

    render(){
        
        
        
       

        const { tbodyData, currentPage, todosPerPage } = this.state;
        const indexOfLastTodo = currentPage * todosPerPage;
        const indexOfFirstTodo = indexOfLastTodo - todosPerPage;
        const currentTbodyData = tbodyData.slice(indexOfFirstTodo, indexOfLastTodo);
        
        

          const pageNumbers = [];
          for (let i = 1; i <= Math.ceil(tbodyData.length / todosPerPage); i++) {
            pageNumbers.push(i);
          }

  //         const renderPageNumbers = pageNumbers.map(number => {
  //           return (
              
  
  // <a id={number}key={number}onClick={this.handleClick} className={this.state.currentPage==number?"active":null}>{number}</a>
  

  //           );
  //         });
 
 
 
  const n=this.state.currentPage%this.state.pagesPerScreen
  if(this.state.currentPage%this.state.pagesPerScreen==0){
    var numarr=pageNumbers.slice(this.state.currentPage-this.state.pagesPerScreen,this.state.currentPage); 
  }
  else{
    var numarr=pageNumbers.slice(this.state.currentPage-n,this.state.currentPage+(this.state.pagesPerScreen-n)); 
  }
   
  
   
      const renderPageNumbers =
      
       numarr.map((number)=>{
        return (
          

          <a id={number}key={number}onClick={this.handleClick} className={this.state.currentPage==number?"active":null}>{number}</a>
      
      
              );
      
        
      })
    
        
     


        return(
          <div>
            <div id="myModal2" className="modalp">


<div className="modal-contentp" >
  <div className="modal-headerp" >
    
  <h2 >Message Box</h2>
  <a><span className="close" id='cls' onClick={this.bt}>&times;</span></a>

  </div>

  <div className="profilep">
        <div className="content-layer" >
        <div >
        <br/>
        <div><h6><center>Do you wish to remove this person ?</center></h6></div>
      </div>
  <div className="submit-part" >
  <button type="submit" className="btn btn-primary" onClick={this.bt} style={{background:"#337ab7"
}}>Close</button>
  <button type="submit" className="btn btn-primary" onClick={this.bt.bind(this)} style={{background:"#337ab7"}}>Delete</button>
  </div>
  </div>
  </div> 
</div>
</div>
                   
          <div className="table_full"> 
            <table className='table'>
                
            <thead >
                <tr  >
                    
                    {/* {this.state.theadData.map((item) => {
                        return(
                            <td>{item}<button  onClick={this.sort_by_room}>SORT</button></td>
                            
                        )
                        
                    })} */}
                    
                <td style={{width:"18vw"}}>Name     <FontAwesomeIcon icon={faArrowDown} className="fa-2xl" aria-hidden="true" onClick={this.sort_by_name} style={{color:"#337ab7"}} /></td>
                    <td style={{width:"18vw"}}>Room <FontAwesomeIcon icon={faArrowDown} className="fa-2xl" aria-hidden="true" onClick={this.sort_by_room} style={{color:"#337ab7"}}/></td>
                   <td style={{width:"18vw"}}>Attendance</td>
                   <td style={{width:"18vw"}}>View</td>
                   <td style={{width:"18vw"}}>Remove</td>


                </tr>
            </thead>


            <tbody>
            
                {currentTbodyData.map((data) => {
                    
                      
                            
                         return( 
                             <tr>
                            <td>{data.items[0]}</td>
                            <td>{data.items[1]}</td>
                           <td style={{width:"18vw"}}>
                           <select id={data.id} onClick={this.attendance_btn} >
                           <option value="">Select</option>
                            <option value="present"  >Present</option>
                            <option value="absent" >Absent</option>
                            </select>
                            </td>
                            <td style={{width:"18vw"}}> <FontAwesomeIcon icon={faEye} className="fa-2xl" aria-hidden="true" style={{color:"#337ab7"}}/></td>
                           
                           <td style={{width:"18vw"}}> <FontAwesomeIcon icon={faTrash} className="fa-2xl" aria-hidden="true" style={{color:"#337ab7"}} onClick={this.show_popup}/></td>
                            </tr>
                         
                         
                         )
                            
                   
                })}
                 
                
            </tbody>
           
        </table>
        
        <center>
        <div class="pagination">
        <a onClick={this.handleClickPrevious}>&laquo;</a>
        {renderPageNumbers}
        <a onClick={this.handleClickNext}>&raquo;</a>
        </div>
        </center>

        </div> 

        </div>
        )
            


       
    }
}


export default Table;